// doctor-page-2

.doctor-page {
    &__flex {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__block {

        &_title {
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 18px;
            line-height: 110%;
            letter-spacing: -0.03em;
            color: var(--tekst---seryy);
            margin-bottom: 20px;
        }
    }

    &__info {
        display: flex;
        gap: 40px;
        padding: 0;

        &_photo {
            flex: 1 0 30%;
            display: block;
            max-width: 428px;
            max-height: 428px;
            position: relative;
        }

        &_img {
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 20px;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_content {
            flex: 1 0 60%;
            padding: 0px;
            display: flex;
            flex-direction: column;
        }

        &_experience {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            color: var(--color-temno-zelenyy);
            width: fit-content;
            margin-bottom: 10px;
        }

        &_fio {
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 40px;
            line-height: 110%;
            letter-spacing: -0.03em;
            color: var(--tekst---temnyy);
            margin-bottom: 48px;
        }

        &_post {
            display: flex;
            gap: 30px;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--akcentnyy---500);
            margin-bottom: 8px;
        }

        &_btns {
            display: flex;
            gap: 20px;
            margin-bottom: 80px;
        }

        &_price {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst---seryy);
            max-width: 176px;

            & span {
                font-weight: 600;
                color: var(--akcentnyy---500);
            }
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }
    }

    &__post {
        &_item {
            font-size: 14px;
            line-height: 140%;
            display: flex;
            gap: 10px;

        }

        &_icon {
            display: block;
            border-radius: 6px;
            padding: 8px;
            min-width: 43px;
            width: 43px;
            height: 43px;
            background: var(--belyy);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &_text {
            margin-bottom: 5px;
        }

        &_post {
            font-weight: 500;
            color: var(--tekst);
        }
    }

    &__rating {
        position: absolute;
        bottom: 8px;
        left: 8px;
        right: 8px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &_estimation {
            font-weight: 700;
            font-size: 20px;
            color: var(--color-temno-zelenyy);
            display: flex;
            gap: 5px;
            border-radius: 12px;
            padding: 8px 12px;
            width: 91px;
            height: 38px;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            color: var(--osnovnoy---500);
            background-color: white;
        }

        &_experience {
            font-weight: 700;
            border-radius: 12px;
            font-size: 20px;
            color: var(--color-temno-zelenyy);
            display: flex;
            gap: 5px;
            padding: 8px 12px;
            width: 141px;
            height: 38px;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            color: var(--osnovnoy---500);
            background-color: white;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            color: var(--tekst---seryy);

            & span {
                font-weight: 600;
                color: var(--osnovnoy---500);
            }
        }

        &_stars {
            display: flex;
            gap: 2px;
        }

        &_star {
            opacity: 0.6;
        }
    }

    &__schedule {
        border: 1px solid #eee;
        border-radius: 16px;
        padding: 28px;
        margin-bottom: 80px;

        &_list {
            display: flex;
        }

        &_item {
            flex-grow: 1;
            color: var(--tekst);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &_item:not(:last-child) {
            border-right: 1px solid rgba(238, 238, 238, 0.93);
        }

        &_time {
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 16px;
            line-height: 130%;
            letter-spacing: -0.01em;
            text-align: center;
            color: var(--akcentnyy---500);

        }

        &_weekend {
            font-weight: 500;
            color: var(--color-zelenyy);
        }
    }

    &__tab {
        &_btns {
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            margin-bottom: 80px;
        }

        &_btn {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            transition: all 0.3s ease;


            &:hover {
                background: var(--color-svetlo-zelenyy-2);
            }

            &.active {
                background: var(--color-svetlo-zelenyy-1);
                color: var(--tekst);
            }
        }
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 20px;

        & li {
            display: flex;
            gap: 12px;
            color: var(--tekst);

            &::before {
                content: "";
                display: block;
                border-radius: 10px;
                min-width: 18px;
                width: 18px;
                height: 3px;
                background-color: var(--color-zelenyy);
                margin-top: 9px;
            }

            & span {
                display: block;
                font-weight: 700;
                color: var(--color-zelenyy);
            }
        }
    }

    &__diplomas {
        &_swiper {
            margin-bottom: 20px;
            height: auto;
        }

        &_swiper-slide {
            border-radius: 10px;
            overflow: hidden;
            background: var(--belyy);

            & a {
                display: block;
                border-radius: 10px;
                overflow: hidden;

                &:hover p {
                    max-height: 200px;
                    opacity: 1;
                    padding: 20px;
                }

                & p {
                    padding: 0 20px;
                    width: 100%;
                    color: var(--tekst);
                    max-height: 0;
                    opacity: 0;
                    transition: all 0.8s ease;
                }
            }

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_swiper-pagination {
            justify-content: center;
        }

        &_picture {
            display: block;
            border-radius: 10px;
            overflow: hidden;
        }
    }
}

.doctor-page__subtitle {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 32px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---temnyy);
    margin-bottom: 40px;
}

.doctor-page .doctor-page__info_btn {
    border-radius: 6px;
    width: 262px;
    height: 60px;
}

.doctor-page__schedule_day {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    text-align: center;
    color: rgba(0, 0, 0, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgba(238, 238, 238, 0.93);
    margin-bottom: 10px;
    padding-bottom: 10px;
    width: 100%;
}

.doctor-page__schedule_day--vs {
    color: var(--osnovnoy---500);
}

.doctor-page__schedule_weekend--vs {
    color: var(--osnovnoy---500);
}

.doctor-page__accordion-btn {
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
    cursor: pointer;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--osnovnoy---500);

}

.accordion-education {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
}

.doctor-page__accordion-content {
    padding-top: 32px;
}

.accordion-education__info p:first-child {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: var(--tekst---seryy);
    margin-bottom: 8px;
}
.accordion-education__svg {
    max-width: 40px;
    max-height: 40px;
    width: 40px;
    height: 40px;
}
.accordion-education__svg svg {
    max-width: 40px;
    max-height: 40px;
    width: 40px;
    height: 40px;
}
.accordion-education__info p:last-child {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--tekst---seryy);
}

// media doctor-page
@media (max-width: 1300px) {
    .doctor-page__info {
        gap: 15px;
    }

    .doctor-page__schedule {
        padding: 15px;
        margin-bottom: 40px;
    }

    .doctor-page__info_photo {
        flex: 1 0 30%;
        display: block;
        max-width: 300px;
        max-height: 300px;
        position: relative;
    }
}

@media (max-width: 1200px) {
    .doctor-page__schedule_list {
        display: flex;
        flex-direction: column;
    }

    .doctor-page__schedule_item {
        display: grid;
        grid-template-columns: auto auto;
        margin-bottom: 12px;
        padding-bottom: 12px;
        justify-content: space-between;
        border-bottom: 1px solid rgba(238, 238, 238, 0.93);
    }

    .doctor-page__schedule_item:last-child {
        margin-bottom: 0px;
        padding-bottom: 12px;
        border-bottom: 1px solid rgba(238, 238, 238, 0.93);
    }

    .doctor-page__schedule_day {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        text-align: center;
        color: rgba(0, 0, 0, 0.4);
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 0px solid rgba(238, 238, 238, 0.93);
        margin-bottom: 0px;
        padding-bottom: 0px;
        width: 100%;
    }
}

@media (max-width: 1000px) {
    .doctor-page {
        &__info {
            display: contents;

            &_photo {
                margin: 0 auto;
            }

            &_content {
                border-radius: 20px;
                padding: 40px;
                background: white;
            }

            &_post {
                flex-direction: column;
                gap: 14px;
            }
        }

        &__post {
            &_item:first-child {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 1px solid #c7d6dc;
            }
        }

        &__schedule {
            &_list {
                flex-direction: column;
            }

            &_item {

                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                gap: 16px;
            }
        }
    }
}

@media (max-width: 688px) {
    .doctor-page {
        &__block {
            border-radius: 10px;
            padding: 20px;
            margin-bottom: 20px;

            &_title {
                font-size: 18px;
                margin-bottom: 20px;
            }
        }

        &__photo {
            max-width: 350px;
        }



        &__info {
            gap: 20px;

            &_content {
                padding: 0px;
            }

            &_fio {
                font-family: var(--font-family);
                font-weight: 600;
                font-size: 26px;
                line-height: 110%;
                letter-spacing: -0.03em;
                color: var(--tekst---temnyy);
                margin-bottom: 32px;
            }

            &_price {
                text-align: center;
                max-width: 100%;
            }

            &_btns {
                flex-direction: column;
                gap: 10px;
                margin-bottom: 40px;
            }
        }

        &__post {
            &_icon {
                min-width: 30px;
                width: 30px;
                height: 30px;
                padding: 5px;
            }
        }
    }

    .doctor-page .doctor-page__info_btn {
        width: 100%;
    }

    .doctor-page__block_title {
        text-align: center;
    }

    .doctor-page__info_photo {
        max-width: 100%;
        max-height: 100%;
    }

}





.doctor-page__accordion {
    user-select: none;
}

.doctor-page__accordion svg {
    transition: transform 0.4s ease;
}

.doctor-page__accordion-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease;
}