// reviews-1
.reviews {
    padding: 160px 0 80px;
    background: var(--fon-plashek);
    overflow: hidden;

    &__cards {
        display: flex;
        gap: 60px;
    }

    &__tab {
        &_btns {
            flex: 1 0 32%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_btn {
            border-radius: 10px;
            padding: 20px;
            background: rgba(255, 255, 255, 0.5);
            opacity: 0.9;
            display: flex;
            align-items: center;
            gap: 16px;
            margin-right: 10px;
            width: 100%;
            &:hover {
                background: var(--belyy);
            }
            &.active {
                background: var(--belyy);
                opacity: 1;
            }
        }

        &_contents {
            flex: 1 0 63%;
        }
    }

    &__photo {
        display: block;
        border-radius: 35px;
        min-width: 50px;
        width: 50px;
        height: 50px;
        overflow: hidden;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__fio {
        font-size: 14px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        text-align: left;
        & p:first-child {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst);
            margin-bottom: 4px;
        }
    }

    &__card {
        &_labels {
            display: flex;
            flex-wrap: wrap;
            gap: 6px 10px;
            margin-bottom: 30px;
            & p {
                font-size: 12px;
                color: var(--color-temno-zelenyy);
                border-radius: 5px;
                padding: 8px 16px;
                background: var(--belyy);
            }
        }

        &_info {
            display: flex;
            flex-direction: column;
            gap: 8px;
            color: var(--tekst-svetlyy);
            margin-bottom: 30px;
            & span {
                font-weight: 500;
                color: var(--tekst);
            }
        }

        &_main {
            margin-bottom: 40px;
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }

        &_publication{
            font-size: 14px;
            line-height: 140%;
            color: rgba(64, 95, 106, 0.7);
            &::before {
                display: inline-block;
                content: url("./../img/icons/calendar.svg");
                width: 18px;
                height: 20px;
                margin-right: 6px;
                vertical-align: middle;
            }
        }

        &_source {
            font-weight: 500;
            font-size: 18px;
        }
    }
}



// media reviews-1
@media (max-width: 1000px) {
    .reviews {
        &__cards {
            flex-direction: column;
            gap: 12px;
        }
        &__tab {
            &_btns {
                display: contents;
            }
            &_contents {
                display: contents;
            }
            &_content {
                padding: 8px 0;
            }
        }
    }
}
@media (max-width: 688px) {
    .reviews {
        padding: 40px 0;
        &__tab {
            &_btn {
                padding: 16px 20px;
            }
        }
        &__card {
            &_labels {
                margin-bottom: 20px;
                & p {
                    padding: 6px 10px;
                }
            }
            &_info {
                gap: 12px;
                margin-bottom: 20px;
            }
            &_main {
                margin-bottom: 20px;
            }
            &_bottom {
                flex-direction: column;
                gap: 10px;
            }
            &_source {
                font-weight: 500;
                font-size: 14px;
            }
        }
    }
}
