/* Общие glob */

html.lock {
    overflow: hidden;
}

html.burger-lock {
    overflow: hidden;
}

html.burger-lock::before {
    opacity: 1;
}

html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}

.no-scroll {
    overflow: hidden;
}

body.lock {
    overflow: hidden;
}

body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    font-family: var(--font-family);
    background-color: var(--fon);
}

.container {
    margin: 0 auto;
    padding: 0 var(--container-padding);
    max-width: var(--container-width);
    width: 100%;
}

.section-offset {
    margin-bottom: 160px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 50px;
}



/*  buttons */
.primary-btn,
.secondary-btn,
.tertiary-btn {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    border-radius: 5px;
    padding: 16px 20px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: .2s;

    &::after {
        content: "";
        background: rgba(8, 11, 15, 0.2);
        position: absolute;
        z-index: -1;
        padding: 0.85em 0.75em;
        display: block;
        left: -2%;
        right: -2%;
        top: 0;
        bottom: -2px;
        transform: skewX(-45deg) scale(0, 1);
        transition: all 0.3s ease;
    }

    & span {
        position: relative;
        z-index: 2;
    }

    &:active {
        transform: scale(0.95);
    }

    &:hover {
        background-position: 0;
    }
}

@media (hover: hover) {

    .primary-btn,
    .secondary-btn,
    .tertiary-btn {
        &:hover::after {
            transform: skewX(-45deg) scale(1, 1);
            transition: all 0.3s ease-out;
        }
    }
}

.primary-btn {
    color: var(--belyy);
    background: var(--color-temno-zelenyy);
    position: relative;
    overflow: hidden;

    &::after {
        background: rgba(8, 11, 15, 0.2);
    }
}

.secondary-btn {
    color: var(--color-zelenyy);
    background: var(--belyy);
    z-index: 1;

    &::after {
        background: var(--color-svetlo-zelenyy-1);
    }
}

.tertiary-btn {
    color: var(--color-temno-zelenyy);
    border: 1px solid var(--color-temno-zelenyy);

    &::after {
        background: var(--color-temno-zelenyy);
    }

    &:hover {
        color: var(--belyy);
    }
}


.btn-arrow {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: left;
    color: var(--color-temno-zelenyy);
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--color-temno-zelenyy);
        border-right: 1px solid var(--color-temno-zelenyy);
        transform: rotate(45deg);
        margin-left: 8px;
        transition: all 0.3s ease;
    }

    &:hover {
        opacity: 0.5;
    }
}

@media (hover: hover) {
    .btn-arrow {
        &:hover {
            opacity: 0.5;

            &::after {
                transform: rotate(45deg) translate(5px, -5px);
            }
        }
    }
}

.btn-circle {
    display: block;
    border-radius: 25px;
    width: 30px;
    min-width: 30px;
    height: 30px;
    background: var(--color-zelenyy);
    color: var(--belyy);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--belyy);
        border-right: 1px solid var(--belyy);
        transform: rotate(45deg);
        margin-right: 4px;
        transition: all 0.3s ease;
    }

    &:hover {
        opacity: 0.5;
    }
}

.btn-more.active {
    &::after {
        transform: rotate(315deg);
    }
}



