/*  form  */

.input {
    background: var(--belyy);
    border-radius: 5px;
    padding: 16px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst);
    border: 1px solid transparent;
    transition: all 0.3s ease;
}

.input::placeholder {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
}

.input:hover {
    background: var(--bg-svetlo-seryy);
}

.input:focus {
    border: 1px solid var(--tegi);
    background: var(--belyy);
}

.input:active {
    border: 1px solid var(--color-zelenyy);
    background: var(--belyy);
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance: none;
}


.input-wrapper {
    position: relative;
    border-radius: 5px;
    overflow: hidden;

    & .input {
        width: 100%;
    }
}

.input-error {
    display: none;
    position: absolute;
    bottom: -3px;
    left: 15px;
    font-size: 11px;
    color: #ff4a37;
}

.input-clear {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    z-index: 5;
    cursor: pointer;
}


.textarea {
    background: var(--belyy);
    border-radius: 5px;
    padding: 16px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
