}

/*  search  */
.form-search {
    position: relative;
    width: 100%;

    & input {
        background: var(--fon-plashek);
        opacity: 0.7;
        width: 100%;

        &:focus {
            opacity: 1;
        }
    }

    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

/* select */
select,
.select {
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    padding: 16px 20px;
    padding-top: 28px;
    font-weight: 500;
    color: var(--tekst);
    opacity: 0.7;
    padding-right: 30px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: var(--fon-plashek) url(./../img/icons/arrow-bottom.svg) no-repeat right;
    background-position-x: calc(100% - 8px);
    transition: all 0.3s ease;

    &:hover {
        opacity: 1;
    }

    &:focus {
        outline: 1px solid var(--color-zelenyy);
    }
}


.sort_block {
    position: relative;

    & label {
        display: block;
        font-size: 12px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        position: absolute;
        top: 10px;
        left: 20px;
        z-index: 2;
    }
}

.sort {
    &__text {
        color: var(--tekst);
        margin-bottom: 20px;
    }

    &__item {
        margin-bottom: 20px;
        display: flex;
        gap: 14px;
        align-items: center;

        & input[type="checkbox"] {
            position: relative;
            width: 28px;
            height: 28px;
            color: #000;
            border: 1px solid #788189;
            border-radius: 5px;
            appearance: none;
            outline: 0;
            cursor: pointer;
            transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);

            &::before {
                position: absolute;
                content: '';
                display: block;
                top: 3px;
                left: 9px;
                width: 8px;
                height: 14px;
                border-style: solid;
                border-color: #fff;
                border-width: 0 3px 3px 0;
                transform: rotate(45deg);
                opacity: 0;
            }

            &:checked {
                color: #fff;
                border-color: var(--color-zelenyy);
                background: var(--color-zelenyy);

                &::before {
                    opacity: 1;
                }
            }
        }

        & label {
            position: relative;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);

            &::before {
                position: absolute;
                content: attr(data-content);
            }
        }
    }
}

/* range */
input[type="range"] {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 4px;
    background: var(--color-svetlo-zelenyy-1);
    border-radius: 20px;
    outline: none;
    -webkit-transition: .2s;
    transition: opacity .2s;

    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 17px;
        height: 17px;
        border-radius: 100%;
        background: var(--color-zelenyy);
        cursor: pointer;
    }

    &::-moz-range-thumb {
        width: 17px;
        height: 17px;
        border-radius: 100%;
        background: var(--color-zelenyy);
        cursor: pointer;
    }

    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }

    &:focus::-moz-range-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }

    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
}




/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab__btn {
    transition: all 0.3s ease;
}

.tabcontent {
    display: none;

    &--active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}

.tab-content {
    display: none;

    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}

/* rating */
.full-stars {
    text-align: center;
}

.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.full-stars input {
    position: absolute;
    left: -9999px;
}

.full-stars label {
    margin: 0;
    cursor: pointer;
}

.full-stars label svg {
    margin: 0 2px;
    height: 18px;
    width: 20px;
    fill: var(--color-temno-zelenyy);
    -webkit-transition: fill 0.3s;
    -o-transition: fill 0.3s;
    transition: fill 0.3s;
}

.full-stars input:checked~label svg {
    fill: transparent;
}

.full-stars .rating-group:hover label svg {
    fill: var(--color-temno-zelenyy);
}

.full-stars .rating-group input:hover~label svg {
    fill: transparent;
}


/* breadcrumbs */
.breadcrumbs {
    margin-bottom: 25px;
}

.breadcrumbs,
.breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 6px;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.breadcrumbs li,
.breadcrumbs li a {
    font-size: 14px;
    color: var(--tekst-temnyy-60);
}

.breadcrumbs li a {
    color: var(--tekst-temnyy-100);
}

.breadcrumbs li a:hover {
    font-weight: 500;
}

.breadcrumbs li:after {
    content: "—";
    display: block;
    font-size: 20px;
    color: var(--tekst-temnyy-100);
    font-size: inherit;
}

.breadcrumbs li:last-child::after {
    content: none;
}


