.toggle-all__arrow {
    transition: transform 0.3s ease;
}

.toggle-all__button.open {
    margin-top: 20px;
}

.toggle-all__arrow.open {
    transform: rotate(180deg);
}

@media (max-width: 1245px) {
    .toggle-all__item {
        flex: 0 0 calc(33% - 12px);
    }
}

@media (max-width: 970px) {
    .toggle-all__item {
        flex: 0 0 calc(50% - 12px);
    }
}

@media (max-width: 650px) {
    .toggle-all__button {
        display: flex;
    }

    .toggle-all__item:not(:last-child) {
        margin-bottom: 16px;
    }

    .toggle-all__items {
        display: flex;
        flex-wrap: wrap;
        gap: 0px;
    }

    .toggle-all__item.extra {
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        visibility: hidden;
        padding: 0;
        margin: 0;
        transition: all 0.4s ease;
    }

    .toggle-all__item {
        flex: 0 0 calc(100%);
    }

    .toggle-all__item.extra.show {
        max-height: 100%;
        opacity: 1;
        visibility: visible;
    }

    .toggle-all__item--picture.extra.show {
        max-height: 100%;
        opacity: 1;
        visibility: visible;
        margin-bottom: 16px;
    }
}

