.custom-select-wrapper {
    position: relative;
    user-select: none;
    font-family: sans-serif;
    margin-bottom: 40px;
}

.custom-select {
    padding: 13px;
    max-width: 353px;
    border-bottom: 1px solid #eee;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    transition: border 0.3s;
}

.select-arrow {
    transition: transform 0.3s ease;
}

.custom-select-wrapper.open .select-arrow {
    transform: rotate(180deg);
}

.custom-options {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-top: none;
    background: white;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
    z-index: 10;
}

.custom-options .selected-option {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: var(--tekst---seryy);
}

