// gallery-3
.gallery {
    &__inner {
        margin-bottom: 30px;
    }

    &__title {
        flex-basis: 40%;
        color: var(--tekst---temnyy);
        text-align: center;
        margin-bottom: 60px;
    }

    &__tab {
        &_btns {
            display: flex;
            width: fit-content;
        }

        &_btn {
            display: block;
            padding: 15px 30px;
            color: var(--tekst-svetlyy);
            border-bottom: 1px solid var(--color-svetlo-zelenyy-1);
            transition: all 0.3s ease;

            &:hover {
                -webkit-text-stroke: 0.5px var(--color-zelenyy);
                text-stroke: 0.5px var(--color-zelenyy);
            }

            &.tab__btn--active {
                -webkit-text-stroke: 0.5px var(--color-zelenyy);
                text-stroke: 0.5px var(--color-zelenyy);
                color: var(--color-zelenyy);
                border-bottom: 1px solid var(--color-zelenyy);
            }
        }
    }

    &__btn-circle {
        display: none;
    }

    &__cards {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__image {
        display: block;
        border-radius: 20px;
        overflow: hidden;
        position: relative;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &:nth-child(1) {
            grid-column: span 2;
            grid-row: span 2;
        }
    }

    &__label {
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        z-index: 5;
        border-radius: 10px;
        padding: 10px 16px;
        background: var(--belyy);
        font-size: 14px;
        color: var(--tekst);
        width: fit-content;
    }

    &__btn {
        display: block;
        margin: 0 auto;
        width: fit-content;
    }
}



// media gallery-3
@media (max-width: 1100px) {
    .gallery__title {
        margin-bottom: 30px;
    }

    .gallery {
        &__inner {
            flex-direction: column;
            gap: 30px;
            align-items: stretch;
        }

        &__tab {
            &_btns {
                width: 100%;
            }

            &_btn {
                flex: 1 0 20%;
            }
        }

        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }

        &__image {
            &:nth-child(1) {
                grid-column: span 1;
                grid-row: span 1;
            }
        }
    }
}

@media (max-width: 940px) {
    .gallery {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            grid-auto-rows: 1fr;
            gap: 20px;
        }
    }
}

@media (max-width: 600px) {
    .gallery {
        &__cards {
            grid-template-rows: minmax(240px, 280px);
            grid-auto-flow: column;
            overflow-x: auto;
            gap: 10px;
            padding-bottom: 30px;
            scrollbar-width: none;
            height: 230px;

            &::-webkit-scrollbar {
                height: 4px;
            }

            &::-webkit-scrollbar-track {
                border-radius: 10px;
                background: var(--izobrazhenie);
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: var(--color-zelenyy);
            }
        }

        &__image {
            min-width: calc(81vw - 40px);
            height: 200px;
            border-radius: 10px;
            scroll-snap-align: start;
        }

        &__btn {
            width: 100%;
        }

        &__label {
            border-radius: 5px;
            padding: 5px 10px;
            font-size: 12px;
        }

        &__tab {
            &_btns {
                flex-direction: column;
                gap: 0;
                max-height: 45px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }

            &_btns.active {
                max-height: 400px;

                .gallery__btn-circle {
                    transform: rotate(270deg);
                }
            }

            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;

                .gallery__btn-circle {
                    opacity: 0;
                }

                &.tab__btn--active {
                    padding: 8px 15px;
                    margin: 0;
                    order: -1;

                    .gallery__btn-circle {
                        opacity: 1;
                    }
                }
            }
        }

        &__btn-circle {
            display: flex;
            background: rgba(61, 124, 133, 0.25);
            color: var(--tekst-svetlyy);
            transform: rotate(90deg);
        }
    }

    .gallery__title {
        text-align: left;
        margin-bottom: 28px;
    }
}