// popup cokie
.popup-cookie {
    position: fixed;
    right: 40px;
    bottom: 40px;
    max-width: 623px;

    &__row {
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
        background: var(--color-temno-zelenyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    &__text {
        line-height: 140%;
        color: var(--belyy);
        flex-basis: 64%;
    }
}

// media popup cokie
@media (max-width: 970px) {
    .popup-cookie {
        right: 10px;
        bottom: 10px;
    }
}
@media (max-width: 688px) {
    .popup-cookie {
        right: 0;
        left: 0;
        bottom: 0;
        max-width: 100%;
        &__row {
            border-radius: 0;
            padding: 14px 16px 16px 16px;
            flex-direction: column;
            align-items: stretch;
            gap: 14px;
        }
        &__text {
            text-align: center;
        }
    }
}