/* Navigation */

.navigation__inner-1:first-child {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    margin-bottom: 20px;
    gap: 20px;
    flex-wrap: wrap;
}

.navigation__inner-1 {
    gap: 20px;
}

.dock-check__inner-1 {
    display: flex;
    flex-direction: column;
    flex-basis: calc(70% - 20px);
    gap: 20px;
}

.doc-check-1 {
    border-radius: 10px;
    padding: 30px;
    background-color: var(--fon-plashek);
    transition: background-color 0.3s ease;
    color: var(--tekst-svetlyy);

    &:hover {
        background-color: var(--color-svetlo-zelenyy-2);
    }
}

.doc-check__name-1 {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.4;
    color: var(--tekst);
    align-items: center;
    display: flex;
}

.doc-check__date-1 {
    color: #B2BAC2;
}

.doc-check__top-1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid rgba(190, 219, 215, 0.4);
}

.doc-check__content-1 {
    display: flex;
    align-items: stretch;
    gap: 20px;
}

.doc-check__picture-1 {
    max-width: 70px;
    border-radius: 10px;
    width: 100%;
    height: auto;
    background: radial-gradient(circle, rgba(255, 255, 255, 1) 0%, rgba(229, 241, 239, 1) 100%);

    & img {
        max-width: 70px;
        object-fit: cover;
        border-radius: 10px;
        width: 100%;
        height: 100%;
        object-position: top;
    }
}

.doc-check__info-1 {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.doc-check__wrap-1 {
    margin-right: 14px;
    padding-right: 14px;
    border-right: 1px solid rgba(190, 219, 215, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.doc-check__content-1 {
    display: flex;
    gap: 20px;
}



.doc-check__info-1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 4px;
}

.navigation__wrap-1 {
    height: 300px;
}

.navigation__wrap-1:first-child {
    flex: 0 0 59%;
}

.navigation__wrap-1:last-child {
    flex: 0 0 39%;
}


.navigation__title-1 {
    margin-bottom: 20px;
}

.navigation__list-1 {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.navigation__right-picture img {
    border-radius: 20px;
}

.navigation__link-1 {
    position: relative;
    transition: all 0.3s ease;
    font-weight: 600;

    &:hover {
        text-decoration: none;
    }
}

.navigation__item-1 {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.56;
}



.navigation__list-1 .navigation__item.nav-title-h2 {
    position: relative;
    cursor: pointer;
    margin-right: 20px;
    padding-right: 20px;






}
.navigation__right__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navigation__right__btns button {
    width: 220px;
    height: 52px;
}

.navigation__wrap-1 {
    position: relative;
}

.navigation__right-text div {
    display: flex;
    align-items: center;
    gap: 8px;
}

.navigation__right-text div:first-child {
    margin-bottom: 16px;
}

.navigation__right-title {
    margin-bottom: 24px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---seryy);
}

.navigation__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.navigation__right-picture {
    position: absolute;
    right: 0;
    bottom: 0;
}
.doc-check__post-1 {
    position: relative;
    padding-left: 20px;


}


.doc-check__stage-1 {
    position: relative;
    padding-left: 20px;

}

.navigation__wrap-1 {
    background: #f5f5f5;
    padding: 30px 32px;
    border-radius: 20px;
}

.navigation__list-1 .nav-title-h3 .navigation__link:after,
.navigation__list-1 .nav-title-h4 .navigation__link:after,
.navigation__list-1 .nav-title-h5 .navigation__link:after {
    display: none;
}

.navigation__list-1 .nav-title-h2 .navigation__link::before,
.navigation__list-1 .nav-title-h3 .navigation__link::before,
.navigation__list-1 .nav-title-h4 .navigation__link::before,
.navigation__list-1 .nav-title-h5 .navigation__link::before {
    content: "";
    display: block;
    min-width: 8px;
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: var(--color-temno-zelenyy);
    margin-top: 7px;
    position: absolute;
}

.navigation__list-1 .nav-title-h3 .navigation__link,
.navigation__list-1 .nav-title-h4 .navigation__link,
.navigation__list-1 .nav-title-h5 .navigation__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
    padding-left: 19px;
}

/* Navigation media */
.navigation__title-1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---seryy);
    margin-bottom: 20px;
}

.navigation__list-1 .nav-title-h2 .navigation__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
    padding-left: 19px;
}

.navigation__list-1 .nav-title-h2 .navigation__link a {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
}

@media (max-width: 1340px) {
    .navigation__wrap-1:first-child {
        flex: 0 0 49%;
    }

    .navigation__wrap-1:last-child {
        flex: 0 0 49%;
    }

}

@media (max-width: 1090px) {
    .navigation__wrap-1:first-child {
        flex: 0 0 100%;
    }

    .navigation__right-picture {
        display: none;
    }

    .navigation__wrap-1:last-child {
        flex: 0 0 100%;
    }

}

@media (max-width: 1024px) {
    .navigation__inner-1 {
        flex-direction: column;
        gap: 0;
    }

    .navigation__right-text {
        display: flex;
        align-items: baseline;
        justify-content: flex-start;
        gap: 8px;
    }


    .navigation__title-1 {
        order: 1;
    }

    .navigation__list-1 {
        order: 2;
        margin-bottom: 20px;
        max-width: 650px;
    }

    .dock-check__inner-1 {
        order: 3;
        flex-basis: 100%;
    }

    .doc-check__content-1 {
        gap: 10px;
    }

}

@media (max-width: 650px) {
    .doc-check-1 {
        flex-wrap: wrap;
    }

    .navigation__right__btns {
        flex-direction: column;
        gap: 10px;
    }
    .navigation__right__btns button {
        width: 100%;
    }

    .doc-check__picture-1 {
        flex-basis: calc(30% - 10px);

    }

    .doc-check__wrap-1 {
        border-right: none;
        padding-right: 0;
        margin-right: 0;
        flex-basis: calc(70% - 10px);
    }


    .navigation__list-1 {
        max-width: 100%;
    }
}

@media (max-width: 500px) {

    .doc-check__name-1 {
        font-size: 14px;
        line-height: 1.1;
    }

    .doc-check-1 {
        padding: 20px;
    }

    .doc-check__picture-1 {
        flex-basis: calc(30% - 5px);

    }

    .doc-check__name-1 {
        flex-basis: calc(70% - 5px);
    }

    .doc-check__content-1 {
        flex-wrap: wrap;
        display: flex;
    }

    .doc-check__info-1 {
        flex-basis: 100%
    }

    .doc-check__post-1 {
        padding-left: 15px;

        &:after {
            top: 4px;
            width: 11px;
            height: 12px;
        }
    }


    .doc-check__stage-1 {
        padding-left: 15px;

        &:after {
            top: 4px;
            width: 11px;
            height: 11px;
        }
    }

    .doc-check__date-1 {
        padding-left: 15px;

        &:after {
            top: 4px;
            width: 11px;
            height: 11px;
        }
    }

    .navigation__list-1 .navigation__item.nav-title-h2 {
        position: relative;
        cursor: pointer;
        margin-right: 20px;
        padding-right: 20px;

        &:after {

            font-size: 20px;

        }

        &:before {
            top: -3px;
            right: -6px;
            width: 24px;
            height: 24px;
        }

    }

    .navigation__list-1 {
        gap: 10px;
    }



}