// text-block-1
.text-block {
    &__grid {
        display: grid;
        grid-template-columns: 9.1fr 3.3fr;
        gap: 40px;

        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }

    &__left,
    &__right {
        padding-top: 20px;

    }

    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
        font-weight: 700;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 32px;
    }

    & h2 {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 48px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
        margin-bottom: 20px;
        padding-top: 10px;

    }

    & h3 {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 28px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
        margin-bottom: 20px;
        padding-top: 10px;
    }

    & h4 {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 20px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
        margin-bottom: 16px;
        padding-top: 10px;
    }

    & h5 {
        font-size: 18px;
    }

    & h2:empty,
    & h3:empty,
    & h4:empty,
    & h5:empty,
    & h6:empty {
        margin-bottom: 0;
    }

    & p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 18px;
        line-height: 130%;
        color: var(--tekst---seryy);

        &:not(:last-child) {
            margin-bottom: 12px;
        }

 
    }

    & span {
        font-weight: 500;
    }

    & a:not(.phone-btn-white) {
        font-weight: 500;
        text-decoration: underline;
        color: var(--tekst-svetlyy);
        transition: all 0.3s ease;

        &:hover {
            color: var(--color-zelenyy);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;

        & li {
            display: flex;
            gap: 10px;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--tekst---seryy);

            &::before {
                content: "";
                display: block;
                min-width: 8px;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background-color: var(--color-zelenyy);
                margin-top: 7px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;

        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;

        & li {
            display: flex;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--tekst---seryy);

            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 21px;
                margin-right: 10px;
                width: 12px;
                height: 21px;
                font-weight: 500;
                font-size: 12px;
                font-family: var(--font-family);
                font-weight: 600;
                font-size: 16px;
                letter-spacing: -0.04em;
                text-align: center;
                color: var(--akcentnyy---500);
                color: var(--tekst-svetlyy);
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__img {
        & img {
            border-radius: 10px;
            width: 100%;
            object-fit: cover;
            margin-bottom: 10px;
        }

        & p {
            font-size: 14px;
            color: rgba(64, 95, 106, 0.8);
            margin: 0;
        }
    }

    &__form {
        border-radius: 20px;
        padding: 40px 80px;
        background: linear-gradient(210deg, #6aa5ae 0%, #3d7c85 100%);
        color: var(--belyy);
        overflow: hidden;
        display: flex;
        gap: 20px;
        margin: 80px 0;

        &_title {
            font-weight: 700;
            font-size: 30px;
            line-height: 120%;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: none;
        border-radius: 10px;
        overflow: hidden;
        border-collapse: collapse;
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                background: var(--color-svetlo-zelenyy-1);
                font-weight: 500;
                font-size: 14px;
                color: var(--tekst);
                border-right: 2px solid #fff;
            }

            & tr th:last-child {
                border-right: none;
            }
        }

        & tbody {
            & td {
                border-right: 2px solid #fff;
                padding: 20px;
                font-size: 14px;
                color: var(--tekst-svetlyy);
                vertical-align: top;
            }

            & tr td:last-child {
                border-right: none;
            }

            & tr:nth-child(even) {
                background: var(--bg-svetlo-seryy);
            }
        }
    }

    &__video {
        position: relative;
        width: 100%;
        border-radius: 10px;
        overflow: hidden;

        & video {
            width: 100%;
            display: block;
        }

        &_play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            z-index: 10;
            transition: all 0.3s ease;

            &:hover {
                transform: translate(-50%, -50%) scale(1.1);
            }
        }
    }


    &__literature {
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 40px;
        }

        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-bottom: 30px;
            margin-bottom: 30px;
            list-style-type: none;
            counter-reset: item;

            & li {
                display: flex;
                gap: 12px;
                color: var(--tegi);
                opacity: 0.85;

                &::before {
                    content: counter(item);
                    counter-increment: item;
                    display: block;
                    min-width: max-content;
                    border: none;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    color: var(--color-temno-zelenyy);
                    opacity: 0.4;
                }
            }
        }

        &_bottom {
            display: flex;
            gap: 20px;
            justify-content: space-between;
        }

        &_item {
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            gap: 15px;
            justify-content: space-between;

            & p {
                margin: 0 !important;
            }
        }

        &_share {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background-color: var(--color-zelenyy);
        }
    }
}



// media text-block
@media (max-width: 1100px) {
    .text-block {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0;
            padding-top: 20px;

        }

        &__right {
            display: contents;
        }

        &__left {
            display: contents;

        }

        & h2,
        & h3,
        & h4,
        & h5,
        & h6 {
            order: -2;
        }

        &__img {
            order: 2;
            margin-bottom: 20px;

            & img {
                max-height: 400px;
            }
        }

        &__form {
            margin: 0 0 20px 0;
            padding: 40px;
        }
    }

    .text-block__right .form {
        display: none;
    }
}

@media (max-width: 688px) {
    .text-block {
        &__grid {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }

        & h2 {
            font-size: 24px;
        }

        & h3 {
            font-size: 22px;
        }

        & h4 {
            font-size: 20px;
        }

        & h5 {
            font-size: 16px;
        }

        & ul li {
            &::before {
                min-width: 8px;
                width: 8px;
                height: 8px;
            }
        }

        &__img {
            & img {
                max-height: 230px;
            }

            & p {
                font-size: 12px;
            }
        }

        &__form {
            border-radius: 10px;
            padding: 20px;
            flex-direction: column;
            gap: 0;

            &_title {
                font-size: 22px;
            }
        }

        & table {
            overflow-x: auto;
            max-width: 100%;

            & thead th,
            & tbody td {
                min-width: 243px;
            }

            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }

        &__literature {
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }

            & ol {
                margin-bottom: 14px;

                & li {
                    font-size: 14px;

                    &::before {
                        font-size: 18px;
                    }
                }
            }

            &_bottom {
                flex-direction: column;
                gap: 14px;
            }

            &_item {
                padding-bottom: 14px;
                font-size: 14px;
                border-bottom: 1px solid #c7d6dc;
                align-items: center;
            }

            &_share {
                font-size: 14px;
            }

            &_links {
                display: flex;
                gap: 10px;
            }

            &_link {
                width: 32px;
                height: 32px;
            }
        }
    }
}

