// licenses-1
.licenses {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 70px;
    }

    &__item {
        flex: 1 0 40%;
        color: #405f6a;
        max-width: 48%;
        & > p {
            margin-bottom: 20px;
        }
    }

    &__top {
        margin-bottom: 50px;
    }

    &__label {
        font-weight: 500;
        color: var(--tekst);
        border-radius: 20px;
        padding: 25px 30px;
        background: var(--fon-plashek);
        width: 70%;
        margin-bottom: 30px !important;
    }

    &__swiper {
        &-slide {
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_label {
            font-weight: 500;
            font-size: 12px;
            line-height: 120%;
            text-align: center;
            color: var(--tekst-svetlyy);
            border-radius: 10px;
            padding: 15px;
            background: #d3ebef;
            width: 100%;
            position: absolute;
            bottom: 0;
            right: 0;
            left: 0;
            z-index: 10;
        }
    }

    &__right {
        max-width: 45%;
    }

    &__text {
        border-radius: 20px;
        padding: 30px;
        background: var(--bg-svetlo-seryy);
        display: flex;
        align-items: center;
        gap: 30px;
        margin-bottom: 30px;

        &_left {
            padding-right: 30px;
            border-right: 1px solid var(--izobrazhenie);
            color: var(--tegi);
            & p:not(:last-child) {
                margin-bottom: 14px;
            }
        }

        &_right {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #003649;
        }
    }

    &__swiper-scrollbar {
        display: none;
    }
}



// media licenses-1
@media (max-width: 1100px) {
    .licenses {
        overflow: hidden;
        &__row {
            display: flex;
            flex-direction: column;
            gap: 0;
        }
        &__item {
            max-width: 100%;
        }
        &__label {
            width: fit-content;
        }
        &__right {
            margin-bottom: 30px;
        }
        &__top {
            margin-bottom: 30px;
        }
        &__info {
            display: contents;
        }
        &__label {
            padding: 20px;
            margin-bottom: 30px !important;
        }
        &__swiper {
            overflow: visible;
            &-btns {
                order: 3;
                gap: 12px;
            }
        }
        &__swiper-scrollbar {
            display: block;
        }
    }
}
