// linking-directions-4
.linking-directions {
    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {
        display: flex;
        flex-direction: column;
        gap: 6px;

        &_top {
            border-radius: 10px;
            height: 200px;
            display: flex;
            align-items: end;
            position: relative;
            overflow: hidden;
        }

        &_photo {
            position: absolute;
            width: 100%;
            height: 100%;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_labels {
            padding: 20px;
            display: flex;
            flex-wrap: wrap;
            gap: 6px;
            position: relative;
            z-index: 2;
            & p {
                border-radius: 5px;
                padding: 6px 10px;
                background: var(--belyy);
                font-size: 14px;
            }
        }

        &_main {
            border: 1px solid var(--color-svetlo-zelenyy-2);
            border-radius: 10px;
            padding: 24px 28px;
            color: var(--color-zelenyy);
        }

        &_name {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--color-zelenyy);
            padding-bottom: 14px;
            border-bottom: 1px dashed var(--color-svetlo-zelenyy-1);
            margin-bottom: 14px;
            transition: all 0.3s ease;
            &:hover {
                opacity: 0.6;
            }
        }

        &_price {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            & p:last-child {
                font-weight: 500;
            }
        }
    }
}

// media linking-directions-4
@media (max-width: 1100px) {
    .linking-directions {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 700px) {
    .linking-directions {
        &__title {
            margin-bottom: 20px;
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
        &__card {
            &_top {
                height: 160px;
            }
            &_labels {
                padding: 10px;
            }
            &_main {
                padding: 18px 20px;
            }
            &_name {
                font-size: 16px;
            }
        }
    }
}