// guarantees-1
.guarantees {
    padding: 160px 0;
    background: var(--fon-plashek);

    &__titles {
        flex-basis: 32%;
        margin-right: 30px;
    }

    &__title {
        margin-bottom: 14px;
    }

    &__subtitle {
        margin-bottom: 30px;
    }

    &__row {
        display: flex;
        gap: 20px;
    }

    &__cards {
        & ul {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & li {
                padding: 20px;
                padding-left: 68px;
                color: var(--tekst-svetlyy);
                position: relative;
                & p:first-child {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 140%;
                    color: var(--tekst);
                    margin-bottom: 14px;
                }
                &::before {
                    content: url("./../img/icons/guarantees-checkmark.svg");
                    position: absolute;
                    top: 20px;
                    left: 20px;
                }
            }
        }
    }

    &__img {
        flex-basis: 35%;
        min-width: 300px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
            border-radius: 20px;
        }
    }
}


// media guarantees-1
@media (max-width: 1000px) {
    .guarantees {
        padding: 100px 0;
        &__row {
            flex-direction: column;
        }
        &__titles {
            display: contents;
        }
        &__title, &__subtitle {
            margin-bottom: 0;
        }
        &__btn {
            order: 3;
        }
        &__img {
            order: 2;
            & img {
                max-height: 460px;
            }
        }
    }
}
@media (max-width: 660px) {
    .guarantees {
        padding: 40px 0;
        &__cards {
            & ul {
                gap: 20px;
                & li {
                    padding: 0;
                    padding-left: 34px;
                    & p:first-child {
                        font-size: 14px;
                        margin-bottom: 8px;
                    }
                    &::before {
                        top: -4px;
                        left: 0;
                        transform: scale(0.8);
                    }
                }
            }
        }
        &__img {
            margin-bottom: 10px;
            min-width: auto;
            & img {
                border-radius: 10px;
                max-height: 280px;
            }
        }
    }
}