// stocks-5
.stocks {
    &__cards {
        margin-bottom: 40px;
    }

    &__swiper .swiper-wrapper {
        flex-direction: column;
    }

    &__card {
        padding: 30px 0;
        border-bottom: 1px solid  var(--izobrazhenie);
        display: flex;
        gap: 60px;
        justify-content: space-between;
        &:first-child {
            border-top: 1px solid var(--izobrazhenie);
        }

        &_main {
            flex: 1 0 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 36px;
        }

        &_name {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 20px;
        }


        &_img {
            flex: 1 0 33%;
            display: block;
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            width: 100%;
            min-height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_date {
            position: absolute;
            top: 16px;
            left: 16px;
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 12px;
            color: var(--color-temno-zelenyy);
            width: fit-content;
        }
    }

    &__swiper-btns {
        display: none;
    }

    &__bottom_btn {
        display: block;
        margin: 0 auto;
        width: max-content;
    }
}



// media stocks-5
@media (max-width: 800px) {
    .stocks {
        &__cards {
            margin-bottom: 30px;
        }
        &__swiper .swiper-wrapper {
            flex-direction: row;
        }
        &__card {
            padding: 24px 0;
            flex-direction: column;
            gap: 20px;
            &_main {
                gap: 30px;
            }
            &_img img {
                max-height: 300px;
            }
        }
        &__bottom_btn {
            display: none;
        }
        &__swiper-btns {
            display: flex;
            gap: 12px;
            margin-bottom: 10px;
        }
    }
}
@media (max-width: 688px) {
    .stocks {
        &__card {
            &_date {
                padding: 8px 10px;
                top: auto;
                bottom: 12px;
                left: 12px;
            }
            &_name {
                font-size: 18px;
                margin-bottom: 12px;
            }
            &_img img {
                max-height: 200px;
            }
        }
    }
}


