// site-map-4
.site-map {
    &__inner {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__accordion {
        display: contents;
    }
    
    &__item {
        &_top {
            margin-bottom: 18px;
            & p, & a {
                font-weight: 700;
                font-size: 22px;
                line-height: 140%;
                color: var(--tekst-svetlyy);
            }
        }

        &_inner {
            &.accordion__content {
                display: contents;
            }
        }

        &_lists {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 40px;
            padding-left: 30px;
            border-left: 1px solid var(--color-svetlo-zelenyy-2);
            &-inner {
                & p {
                    font-weight: 500;
                    font-size: 18px;
                    margin-bottom: 14px;
                }
            }
        }

        &_list {
            padding-left: 20px;
            border-left: 1px solid var(--color-svetlo-zelenyy-2);
            display: flex;
            flex-direction: column;
            gap: 12px;
            & a {
                color: var(--tekst-svetlyy);
                &:hover {
                    color: var(--color-zelenyy);
                }
            }
        }
    }

    &__brn-more {
        display: none;
    }
}

// media site-map-4
@media (max-width: 1000px) {
    .site-map {
        &__item {
            &_lists {
                grid-template-columns: repeat(2, 1fr);
                gap: 40px;
            }
        }
    }
}
@media (max-width: 700px) {
    .site-map {
        &__inner {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
        &__accordion {
            display: block;
            &.accordion--active {
                & p::after {
                    transform: rotate(315deg);
                }
                & .site-map__brn-more svg path:last-child{
                    transform: rotate(90deg);
                }
            }
        }
        &__item {
            &_top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 20px;
                margin-bottom: 20px;
                & p, & a {
                    font-size: 18px;
                }
            }
    
            &_inner {
                &.accordion__content {
                    display: block;
                }
            }

            &_inner-service {
                &.accordion__content--active {
                    max-height: none !important;
                }
            }
    
            &_lists {
                grid-template-columns: repeat(1, 1fr);
                gap: 10px;
                padding-left: 0;
                border-left: none;
                &-inner {
                    & p {
                        font-size: 14px;
                        &::after {
                            content: "";
                            display: inline-block;
                            width: 8px;
                            height: 8px;
                            border-top: 1px solid var(--color-temno-zelenyy);
                            border-right: 1px solid var(--color-temno-zelenyy);
                            transform: rotate(135deg);
                            margin-left: 10px;
                            margin-bottom: 2px;
                            transition: all 0.2s ease;
                        }
                    }
                }
            }
        }
    
        &__brn-more {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 28px;
            height: 28px;
            border-radius: 20px;
            border: 1px solid var(--tekst-svetlyy);
            & svg path {
                transition: all 0.3s ease;
            }
        }
    }
}

