// intro-1
.intro-1 {
    padding: 100px 0;


    &__right {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__left {
        max-width: 100%;
    }

    &__main {
        border-radius: 10px;
        padding: 0px 0px 65px 0px;
        background: var(--belyy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;


        & h1,
        h2 {
            color: var(--tekst);
            margin-bottom: 15px;
        }

    }


    &__form {
        border-radius: 10px;
        padding: 40px;
        background: var(--belyy);

        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 5px;
        }

        &_phone {
            margin-bottom: 12px;
        }

        &_subtitle {
            margin-bottom: 30px;
        }

        &_inner {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_input {
            width: 100%;
            background: var(--bg-svetlo-seryy);
        }
    }

    &__cards ul {
        display: flex;
        gap: 15px;
        justify-content: space-between;

        & li {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--tekst---temnyy);
            display: flex;
            align-items: center;
            gap: 14px;
        }
    }

    &__swiper {
        height: 85%;
        margin-bottom: 20px;

        &-btns {
            position: absolute;
            right: 80px;
            bottom: 70px;
        }
    }
}

.intro-1__cards {
    padding: 0px 42px;
}

.intro-1__main_btn {
    border-radius: 6px;
    padding: 20px 24px;
    width: 300px;
    height: 60px;
    background: var(--osnovnoy---500);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---svetlyy);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 80px;
}

.intro-1__subtitle {
    max-width: 624px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: rgba(73, 55, 55, 0.8);
    margin-bottom: 40px;
}

.section__inner--main .price__table-titles {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: rgba(102, 102, 102, 0.5);
    display: grid;
    grid-template-columns: 9.1fr 3.3fr;
    gap: 40px;
    margin-bottom: 20px;
}

.intro-1__main h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 60px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---temnyy);
    margin-bottom: 32px;
    max-width: 1000px;
}

.intro-1__main h1 span {
    color: rgba(255, 255, 255, 0.99);
    border-radius: 20px;
    background: var(--osnovnoy---500);
    padding: 0px 10px;
}

.intro-1__main {
    position: relative;
}

.intro-1__main_photo {
    position: absolute;
    top: auto;
    left: auto;
    bottom: 158px;
    right: 0;
}

.intro-1__cards ul {
    flex-wrap: wrap;
}

// media intro-1
@media (max-width: 1362px) {
    .intro-1__main_photo {
        display: none;
    }

    .intro-1__cards ul li {
        width: 49%;

    }

    .intro-1__cards {
        padding: 0px;
    }
}

@media (max-width: 1050px) {
    .intro-1 {
        padding: 40px 0;

        &__row {
            flex-direction: column;
        }

        &__right {
            flex-direction: column-reverse;
        }

        &__left {
            max-width: 100%;
        }

        &__swiper {
            margin-bottom: 0;
            height: 100%;
        }

        &__cards_mobile {
            display: none;
        }
    }

    .intro-1__main h1 {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 30px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
