}

.price__mode {
    margin-bottom: 0px;
}

@media (max-width: 840px) {
    .intro-1__cards ul li {
        width: 100%;
    }

    .section__inner--main .price__table-titles {
        display: none;
    }

}

@media (max-width: 688px) {
    .intro-1 {
        padding: 32px 0;
        overflow: hidden;
        margin-bottom: 0;

        &__swiper {
            overflow: visible;
        }

        &__row {
            flex-direction: column;
        }

        &__right {
            flex-direction: column-reverse;
        }

        &__left {
            max-width: 100%;
        }

        &__main {
            padding: 0px;
            gap: 0px;
            height: 100%;

            & h1,
            h2 {
                margin-bottom: 20px;
            }

            &_btn {
                width: 100%;
                margin-bottom: 0;
            }
        }

        &__form {
            padding: 20px;

            &_title {
                font-size: 18px;
            }

            &_subtitle {
                margin-bottom: 20px;
            }
        }

        &__cards ul {
            gap: 10px;

        }

        &__swiper-btns,
        &__cards_mobile {
            display: none;
        }
    }

    .intro-1__subtitle {
        margin-bottom: 24px;
    }

    .intro-1__main_bottom {
        display: flex;
        flex-direction: column-reverse;
        gap: 28px;
    }
}

