    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: rgba(102, 102, 102, 0.5);
    display: grid;
    grid-template-columns: 4.6fr 4.3fr 3fr 1.5fr 3fr;
    gap: 40px;
    padding: 24px;
}

.section__inner--services .price__name {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--tekst---temnyy);
}

