    .section__inner--services .price__item {
        gap: 10px;
    }


    .section__inner--services .price__table-titles {
        gap: 10px;
    }
}

@media (max-width: 840px) {
    .section__inner--services .change-item__title {
        position: relative;
        padding-right: 32px;
        width: 100%;
        display: block;
        cursor: pointer;
    }

    .price .price__title {
        margin-bottom: 0px;
    }

    .section__inner--services .change-item__title::after {
        content: '';
        position: absolute;
        top: 12px;
        right: 0;
        width: 24px;
        height: 24px;
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"><rect width="24" height="24" rx="12" fill="%23D31D44"/><path d="M8 10.5L12 14.5L16 10.5" stroke="white"/></svg>');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        transform: translateY(-50%);
        transition: transform 0.3s ease;
        pointer-events: none;
        /* чтобы клик шел по родителю */
    }

    /* При открытом блоке — поворачиваем стрелку вниз */
    .price__item.active .change-item__title::after {
        transform: translateY(-50%) rotate(180deg);
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"><rect width="24" height="24" rx="12" fill="%23EFD3D9"/><path d="M8 10.5L12 14.5L16 10.5" stroke="%23D31D44" stroke-width="2"/></svg>');
    }


    .section__inner--services .price__table-titles {
        display: none;
    }

    .section__inner--services .price__item {
        display: flex;
        flex-direction: column;
    }

    .price__for-title,
    .price__date-title {
        display: block;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 120%;
        color: rgba(0, 0, 0, 0.4);
        margin-bottom: 6px;
    }

    .section__inner--services .price__item td:not(.price__name):not(.price__cost) {
        max-height: 0;
        overflow: hidden;
        padding: 0;
        opacity: 0;
        transition: all 0.3s ease;
    }

    .section__inner--services .price__item.active td {
        opacity: 1;
        transition: all 0.3s ease;
    }

    .section__inner--services .price__item.active td:not(.price__name):not(.price__cost) {
        opacity: 1;
        max-height: 100%;
        transition: all 0.3s ease;
        border-bottom: 1px solid #d9d9d9;
        padding-bottom: 12px;
    }

    .section__inner--services .price__name {
        font-size: 16px;
        width: 100%;
    }



    .section__inner--services .price__item {
        gap: 1px;
        padding: 12px 16px;
    }

    .section__inner--services .price__item.active {
        gap: 10px;
    }

}