.popup-time {
    &__content {
        max-width: 1180px;
        border-radius: 20px;
        padding: 60px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--bg-svetlo-seryy);
        display: flex;
    }

    &__left {
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
        margin-right: 40px;
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 420px;
    }

    &__title {
        font-weight: 700;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst);
    }

    &__subtitle {
        line-height: 150%;
        color: var(--tekst-svetlyy);
        margin-bottom: 56px;
    }

    &__bottom {
        width: 100%;
        height: 100%;
        position: relative;
    }

    &__img img {
        width: 100%;
        height: 100%;
        border-radius: 20px;
        object-fit: cover;
    }

    &__phone {
        max-width: 270px;
        position: absolute;
        bottom: 29px;
        right: 29px;
    }

    &__phone p {
        font-size: 14px;
        color: var(--tekst-svetlyy);
        margin-bottom: 6px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__input:hover {
        background-color: var(--belyy);
    }

    &__close {
        width: 30px;
        height: 30px;
        top: 15px;
        right: 15px;
    }
}

.popup-time-response {
    &__content {
        max-width: 583px;
        padding: 10px;
        position: relative;
        display: block;
    }
    &__top {
        position: absolute;
        top: -70px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 140px;
        height: 140px;
        border-radius: 100px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.07);
        background-color: var(--belyy);
        padding: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: "";
            display: block;
            width: 50px;
            height: 35px;
        }
    }

    &__right {
        padding: 10px;
    }
    &__text {
        padding: 90px 38px 40px;
        text-align: center;
        & h3 {
            font-weight: 700;
            font-size: 30px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 10px;
        }
    }
    &__btns {
        display: flex;
        gap: 10px;
    }

    &__btn {
        display: block;
        width: 100%;
        &_ok {
            padding: 10px 20px;
            width: 100%;
        }
    }
}

.popup[data-target="popup-time-ok"] {
    & .popup-time-response__top {
        &::before {
            content: "";
            background-image: url("./../img/popups/popup-time-2-ok.svg");
            background-size: 100% 100%;
        }
    }
}
.popup[data-target="popup-time-error"] {
    & .popup-time-response__top {
        &::before {
            content: "";
            background-image: url("./../img/popups/popup-time-2-error.svg");
            background-size: 100% 100%;
        }
    }
}


// media popup-time-2
@media (max-width: 970px) {
    .popup-time {
        &__content {
            max-width: 700px;
            flex-direction: column;
            gap: 20px;
        }
        &__left {
            padding-right: 0;
            border-right: none;
            margin-right: 0;
            padding-bottom: 20px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__right {
            max-width: 100%;
        }
        &__title {
            font-size: 28px;
        }
        &__subtitle {
            margin-bottom: 20px;
        }
        &__img img {
            border-radius: 10px;
            max-height: 220px;
        }
    }
    .popup-time-response {
        &__content {
            max-width: 530px;
        }
    }
}
@media (max-width: 600px) {
    .popup-time {
        &__content {
            border-radius: 10px;
            padding: 30px 20px 20px 20px;
        }
        &__title {
            font-size: 18px;
        }
        &__bottom {
            display: flex;
            flex-direction: column-reverse;
            gap: 20px;
        }
        &__phone {
            max-width: 100%;
            position: static;
            & p {
                font-size: 12px;
                color: var(--tekst-svetlyy);
                margin-bottom: 6px;
            }
            & a {
                font-size: 16px;
            }
        }
        &__close {
            top: 5px;
            right: 5px;
        }
    }

    .popup-time-response {
        &__content {
            max-width: 430px;
            padding: 10px;
        }
        &__top {
            top: -40px;
            width: 80px;
            height: 80px;
            padding: 20px;
            &::before {
                width: 40px;
                height: 25px;
            }
        }
    
        &__right {
            padding: 10px;
        }
        &__text {
            padding: 46px 10px 26px;
        }
        &__btns {
            flex-direction: column;
        }
    }
}

