/* linking */

.linking__title-5 {
    margin-bottom: 50px;
}

.linking__inner-5 {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;


    &>* {
        flex-basis: calc(25% - 15px);
    }
}

.linking__item-5 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--fon-plashek);
    padding: 24px 20px;
    border-radius: 10px;
    color: var(--tekst-svetlyy);
    font-size: 16px;
    align-items: flex-start;
    flex-direction: column;
    height: auto;
}

.linking__item-bottom-5 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-basis: 100%;
    width: 100%;
    border-top: 1px solid var(--decor-gray);
    margin-top: 14px;
    padding-top: 8px;
}

.linking__item-title-5 {
    font-weight: 700;
    flex-basis: 100%;
}

.linking__item-price-5 {
    font-weight: 500;
}

.linking__btn-5 {
    max-width: 30px;
    max-height: 30px;
    min-width: 30px;
    min-height: 30px;
}

.all__linking-5 {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* linking media */
@media (max-width: 1200px) {
    .linking__inner-5 {
        &>* {
            flex-basis: calc(33.333% - 13.33333px);
        }
    }
}

@media (max-width: 1024px) {
    .linking__item-5 {
        font-size: 14px;
    }
}

@media (max-width: 930px) {
    .linking__inner-5 {
        &>* {
            flex-basis: calc(50% - 10px);
        }
    }
}

@media (max-width: 550px) {
    .linking__inner-5 {
        gap: 10px;
    }
    .linking__item-5 {
        flex-basis: 100%;
    }

    .linking__title-5 {
        margin-bottom: 20px;
    }

    .all__linking-5 {
        flex-basis: 100%;
    }
}