// licenses-2
.licenses-page__inner-1 {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}


.licenses-page__info-1 {
    border-radius: 20px;
    padding: 30px;
    background: #f2f5f9;
    display: flex;
    align-items: center;
    justify-content: space-between;

    & p {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.licenses-page__info-wrap-1 {
    padding-right: 30px;
    margin-right: 30px;
    border-right: 1px solid #c7d6dc;
    flex-basis: 65%;
}

.licenses-page__picture-1 {
    max-height: 430px;
    border-radius: 20px;
    overflow: hidden;

    & img {
        width: 100%;
        height: 100%;
        border-radius: 20px;
        max-height: 430px;
        object-fit: cover;
    }
}

.licenses-page__container-1 {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.licenses-page__label-1 {
    flex-basis: 35%;
    font-weight: 500;
    line-height: 140%;
    color: var(--tekst);
    text-align: start;
}

.licenses-page__swiper-btns-1 {
    display: none;
}

.licenses-page__item-1 {
    position: relative;
    border-radius: 20px;
    max-height: 430px;
    overflow: hidden;
}

.licenses-page__slide-label-1 {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #F2F5F9;
    padding: 15px;
    border-radius: 20px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: var(--tekst-svetlyy);
    font-size: 14px;
}

.licenses-page__slide-1 {
    border-radius: 20px;
    overflow: hidden;
}

.licenses-page__container-1 {
    padding: 0;
}

.licenses-page__info p:first-child {
    margin-bottom: 12px;
}

.toggle-all__items-licenses  {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
}

@media (max-width: 600px) {
    .gallery__image-licenses {
        height: 100%;
    }

}

// media licenses-2

@media (max-width: 1380px) {
    .licenses-page__swiper-1 {
        padding: 0 20px;
    }

    .licenses-page__info-1 {
        margin: 0 20px 20px 20px;
    }
}


@media (max-width: 1100px) {
    .licenses-page__info-1 {
        flex-direction: column;
    }

    .licenses-page__info-wrap-1 {
        border-right: none;
        padding-right: 0;
        margin-right: 0;
        border-bottom: 1px solid #c7d6dc;
        padding-bottom: 20px;
        margin-bottom: 20px;
    }
}

@media (max-width: 650px) {
    .licenses-page__swiper-btns-1 {
        display: flex;
        width: 100%;
        margin: 0 20px;
    }

    .licenses-page__picture-1 {
        border-radius: 10px;

        & img {
            border-radius: 10px;
        }
    }

    .licenses-page__item-1.img-animation {
        border-radius: 10px;
    }

    .licenses-page__item-1.img-animation:before {
        border-radius: 10px !important;
    }

    .licenses-page__slide-1 {
        border-radius: 10px;
    }

    .licenses-page__slide-label-1 {
        border-radius: 10px;
        padding: 12px;
    }

}