// footer-5
.footer {
    padding: 0px 0 50px;

    &__logo {
        display: block;
        max-width: 149px;
        width: 100%;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 48px;

        &_left {
            display: flex;
            align-items: center;
            gap: 122px;
        }

        &_right {
            display: flex;
            align-items: center;
            gap: 40px;
        }
    }

    &__networks {
        display: flex;
        gap: 6px;

        & a {
            display: block;
            width: 45px;
            height: 45px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 0.4s ease;

            & svg path {
                transition: all 0.4s ease;
            }

            &:hover {
                & svg path {
                    fill: var(--belyy);
                }
            }
        }
    }

    &__btns {
        display: flex;
        gap: 12px;


    }

    &__btns-search {
        display: flex;
        gap: 10px;

        & button {
            min-width: 44px;
            height: 44px;
            border-radius: 5px;
            border: 1px solid rgba(255, 255, 255, 0.6);
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &__btn-question {
        border: 1px solid var(--belyy);
        color: var(--belyy);
        transition: background 0.2s ease;
    }

    &__inner {
        padding-bottom: 35px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        margin-bottom: 32px;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 30px;

        &_left {
            flex-basis: 837px;
        }

        &_right {
            flex-basis: 260px;
        }

        &_title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            text-transform: uppercase;
            color: var(--akcentnyy---500);
            border-bottom: 1px solid rgba(2, 96, 96, 0.4);
            margin-bottom: 32px;

        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 28px;

        &_item {
            &>p:first-child {
                font-family: var(--font-family);
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
                color: #454545;
                margin-bottom: 8px;
            }
        }

        &_text {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            flex-direction: column;

            & * {
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: var(--belyy);
            }

            & span {
                display: block;
                font-weight: 400;
            }

            & a:hover,
            & button:hover {
                text-decoration: underline;
            }
        }

        &_phone {
            & a {
                font-family: var(--font-family);
                font-weight: 700;
                font-size: 22px;
                line-height: 140%;
                color: #454545;
            }
        }


    }

    &__pays {
        & p {
            font-size: 14px;
            color: var(--izobrazhenie);
            margin-bottom: 8px;
        }

        &_list {
            display: flex;
            gap: 10px;
            margin-top: 12px;

            & img {
                display: block;
                width: 80px;
                height: 53px;
                padding: 8px 20px;
                border-radius: 10px;
                background: var(--bg-svetlo-seryy);
            }
        }
    }

    &__links {
        display: flex;
        flex-direction: column;
        gap: 10px;

        & a {
            font-size: 14px;
            text-decoration: underline;
            color: var(--izobrazhenie);

            &:hover {
                opacity: 0.7;
            }
        }
    }

    &__nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 56px;

        &_item {
            flex: 1 0 28%;

            & p {
                font-weight: 700;
                font-size: 18px;
                line-height: 140%;
                color: var(--belyy);
                margin-bottom: 10px;
            }

            & ul {
                display: flex;
                flex-direction: column;
                gap: 10px;

                & a {
                    font-family: var(--font-family);
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 140%;
                    color: #454545;
                    transition: all 0.3s ease;

                    &:hover {
                        color: var(--belyy);
                        opacity: 0.7;
                    }
                }
            }
        }
    }

    &__nav-menu {
        flex: 1 0 28%;

        & ul {
            display: block;
            columns: 2;
            column-gap: 40px;

            & a {
                display: block;
                font-weight: 700;
                font-size: 18px;
                color: var(--belyy);
                margin-bottom: 24px;
            }
        }

        & .footer__row_title {
            display: none;
        }
    }

    &__copy {
        font-size: 12px;
        line-height: 140%;
        color: rgba(255, 255, 255, 0.6);

        & p:first-child {
            margin-bottom: 10px;
        }
    }
}

.footer__btns .footer__btn-node {
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
    padding: 10px 8px;
    width: 164px;
    height: 45px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
}

.footer__info_social {
    display: none;
}



.footer__btns .footer__btn-quest {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    border-radius: 6px;
    width: 163px;
    border: 1px solid var(--osnovnoy---500);
}

.footer__btns-mobil .footer__btn-node {
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
    width: 164px;
    height: 51px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;

}

.footer__info_social {
    display: none;
}



.footer__btns-mobil .footer__btn-challenge {
    background: #fbecef;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: #d11039;
    border-radius: 6px;
    height: 52px;
    padding: 14px 8px;
    width: 163px;
    height: 52px;
    border: 1px solid rgba(255, 255, 255, 0);
    transition: background 0.2s ease;
}

.footer__bottom {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.footer__copy--info {
    width: 54%;
}

.footer__bottom .footer__copy--info .footer__copy-name {
    margin-bottom: 8px;
}



.footer__info_address button {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.footer__info_email a {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.footer__info_time p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.footer__pays p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #454545;
}

.footer__info_address span {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.footer__copy {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.6);
}

.footer__links a {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #454545;
}

.footer .footer__btns-mobil {
    display: none;
}

.footer__btns .footer__btn-challenge:hover {
    background-color: white;
    color: var(--osnovnoy---500);
    border: 1px solid var(--osnovnoy---500);
    transition: background 0.2s ease;
}

.footer__nav_item .footer__nav_acc {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 140%;
    color: #454545;
    cursor: pointer;
}

.footer .logo__picture img {
    max-width: 230px;
    max-height: 51px;
    min-width: 230px;
    min-height: 51px;
    width: 100%;
}

.footer__plashka {
    height: 51px;
    background: var(--akcentnyy---500);
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    color: var(--tekst---svetlyy);
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    text-align: center;
    justify-content: center;
}

.footer__plashka p {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

// media footer-5
@media (max-width: 1160px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 36px;

            &_left,
            &_right {
                flex-basis: 100%;
            }
        }
    }
}

@media (max-width: 1000px) {
    .footer__networks {
        display: none;
    }
}

@media (max-width: 900px) {
    .footer__bottom {
        flex-direction: column;
        gap: 10px;
    }

    .footer__plashka {
        margin-bottom: 20px;
    }

    .footer__plashka svg {
        display: none;
    }


}

@media (max-width: 800px) {
    .footer .logo__picture img {
        max-width: 149px;
        max-height: 32px;
        min-width: 149px;
        min-height: 32px;
        width: 100%;
    }

    .footer .footer__btns-mobil {
        display: flex;
        order: 0;
        flex-direction: column;
    }

    .footer__top {
        margin-bottom: 0px;
    }

    .footer__pays_list {
        padding-bottom: 24px;
        border-bottom: 1px solid #eee;
    }

    .footer__btns {
        display: none;
    }

    .footer {
        padding: 40px 0 20px;

        &__plashka {
            font-size: 12px;
        }

        &__top {

            &_left {
                justify-content: space-between;
                gap: 20px;
                margin-bottom: 40px;
            }

            &_right {
                display: contents;
            }
        }

        &__logo {
            max-width: 129px;
        }

        &__inner {
            display: flex;
            flex-direction: column;
            padding-bottom: 30px;
            margin-bottom: 0px;
        }

        &__row {
            &_title {
                font-size: 16px;
                padding-bottom: 14px;
                margin-bottom: 20px;
            }
        }

        &__networks {
            & a {
                width: 35px;
                height: 35px;
            }
        }

        &__btns {
            order: 6;


        }

        &__btns-search {
            // order: 5;
            // margin-bottom: 20px;

            & button {
                min-width: 30px;
                width: 30px;
                height: 30px;
            }
        }

        &__info {
            margin-bottom: 20px;

            &_row {
                flex-direction: column;
                gap: 20px;
            }

            &_item {
                & p:first-child {
                    font-size: 12px;
                }
            }

            &_text {
                align-items: center;

                &>* {
                    font-size: 14px;
                    font-weight: 500;
                }

                & button,
                & span {
                    display: inline-block;
                    font-size: 14px;
                    font-weight: 500;
                }
            }

            &_phone a {
                font-size: 18px;
            }
        }

        &__nav {
            gap: 20px;

            &_item {
                flex-basis: 100%;

                &>p {
                    font-weight: 500;
                    font-size: 16px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    gap: 14px;
                    margin: 0;
                    transition: all 0.4s ease;

                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        border-top: 1px solid #d11039;
                        border-right: 1px solid #d11039;
                        transform: rotate(135deg);
                        margin-bottom: 4px;
                        transition: all 0.3s ease;
                    }

                    &.active {
                        margin-bottom: 12px;

                        &::after {
                            transform: rotate(315deg);
                        }
                    }
                }

                &>ul {
                    max-height: 0;
                    overflow: hidden;
                    transition: all 0.3s ease;

                    &.active {
                        max-height: 500px;
                    }
                }
            }
        }

        &__nav-menu {
            margin-top: 20px;

            & ul {
                & a {
                    font-size: 16px;
                }
            }

            & .footer__row_title {
                display: block;
            }
        }

        &__pays {
            & p {
                font-size: 12px;
            }

            &_list {
                & img {
                    width: 80px;
                    height: 45px;
                    border-radius: 5px;
                }
            }
        }

        &__copy {
            font-size: 12px;
        }
    }

    .footer .footer__btns-mobil .footer__btn-node {
        width: 100%;
    }

    .footer__nav_acc {
        display: block;
    }

    .footer__info_social {
        display: block;
    }

    .footer__info_text {
        align-items: start;
    }
}

@media (max-width: 600px) {
    .footer {
        &__btns {
            flex-direction: column;
        }
    }



    .footer .footer__btns-mobil .footer__btn-challenge {
        width: 100%;
    }
}

.footer__btns-mobil .footer__btn-quest {
    width: 100%;
    height: 51px;
}