// intro-article-2
.intro-article {
    position: relative;
    padding: 20px 0 40px;
    overflow: hidden;

    &__breadcrumbs {
        color: var(--belyy);
        padding-bottom: 12px;
        border-bottom: 1px solid #c7d6dc;
        margin-bottom: 30px;
    }

    &__img-bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__row {
        display: flex;
        gap: 40px;
    }
    &__main {
        display: flex;
        flex-direction: column;
        gap: 40px;
        flex: 1 0 55%;

        & h1, h2 {
            color: var(--belyy);
            margin-bottom: 15px;
        }
        & p {
            color: var(--belyy);
        }
        &_marks {
            display: flex;
            gap: 20px;
        }
    }

    &__mark {
        border-radius: 5px;
        padding: 8px 10px;
        background: var(--color-svetlo-zelenyy-2);
        display: flex;
        align-items: center;
        gap: 10px;
        font-weight: 700;
        font-size: 14px;
        line-height: 140%;
        color: var(--belyy);
        & span {
            font-weight: 400;
        }
    }

    &__right {
        flex-basis: 40%;
    }

    &__form {
        border-radius: 15px;
        padding: 30px;
        background: var(--belyy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        height: 100%;
        position: relative;
        overflow: hidden;
        &_text, &_phone {
            max-width: 60%;
            position: relative;
            z-index: 2;
        }
        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 16px;
        }
        &_subtitle {
            font-size: 14px;
            line-height: 120%;
            color: var(--tekst-svetlyy);
        }
        &_phone {
            background: var(--bg-svetlo-seryy);
        }
        &_img {
            position: absolute;
            bottom: 0;
            right: -20px;
            max-width: 60%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

// media intro-article-2
@media (max-width: 1000px) {
    .intro-article {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
    }
}
@media (max-width: 550px) {
    .intro-article {
        &__main {
            &_bottom {
                flex-direction: column;
                gap: 10px;
            }
            &_marks {
                & span {
                    display: none;
                }
            }
        }
        &__form {
            padding: 20px;
            &_text, &_phone {
                max-width: 100%;
            }
            &_phone {
                width: 100%;
                justify-content: center;
            }
            &_title {
                margin-bottom: 4px;
            }
            &_img {
                display: none;
            }
        }
    }
}
