// advantages-2
.advantages {
    padding: 160px 0;
    background: var(--tekst---svetlyy);

    &__cards {
        display: flex;
        flex-wrap: wrap;
    }


    &__card {
        flex: 1 0 25%;
        padding: 30px;
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 11px;
        text-align: center;

        &_top {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
        }

        & p {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: -0.01em;
            text-align: center;
            color: var(--tekst---seryy);
        }

        &:nth-child(2),
        &:nth-child(4n),
        &:nth-child(-n + 5) {
            border-right: 1px solid #c7d6dc;
        }

        &:nth-child(-n + 3) {
            border-bottom: 1px solid #c7d6dc;
        }
    }
}

.advantages__top {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px;
}


// media advantages-2
@media (max-width: 1120px) {
    .advantages__top {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 60px;
    }

    .section__inner {
        overflow-x: auto;
        overflow-y: hidden;
        -ms-overflow-style: none;
        scrollbar-width: none;
    }

    .advantages__cards {
        display: flex;
        flex-wrap: nowrap;
        width: 1400px;
    }

}

@media (max-width: 1050px) {
    .advantages {
        padding: 80px 0;

        &__card {
            // flex: 1 0 45%;

            &:nth-child(2),
            &:nth-child(4n),
            &:nth-child(4n + 1) {
                border-right: none;
            }

            &:nth-child(2n-1) {
                border-right: 1px solid #c7d6dc;
            }

            &:nth-child(-n + 4) {
                border-bottom: 1px solid #c7d6dc;
            }
        }
    }
}

@media (max-width: 650px) {
    .advantages__top {
        display: flex;

        justify-content: left;
        margin-bottom: 26px;
    }

    .advantages {
        padding: 40px 0;

        &__cards {
            gap: 0;
            margin-bottom: 10px;
        }

        &__card {
            flex: 1 0 16%;
            padding: 20px 0;
            gap: 12px;
            border-bottom: 0px solid #c7d6dc;

            &_top {
                font-size: 16px;
            }

            & img {
                height: 60px;
            }

            &:nth-child(2n-1) {
                border-right: none;
            }
        }
    }

    .advantages__card:nth-child(-n+4) {
        border-bottom: 0px solid #c7d6dc;
    }
}
.how-prepare {
    background: var(--akcentnyy---500);
    padding: 160px 0px;
}


.how-prepare__item-title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 48px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---svetlyy);
    text-align: left;
}







.how-prepare__item-text p:first-child {
    margin-bottom: 12px;
}

.how-prepare__item-text p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: rgba(255, 255, 255, 0.8);
}

.how-prepare__item-accordion p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: rgba(255, 255, 255, 0.8);
    padding-bottom: 20px;
}

.item-accordion-btn {
    margin-bottom: 16px;
}

.item-accordion-btn span {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---svetlyy);
}










.how-prepare__items {
    display: flex;
    gap: 100px;
}

.how-prepare__item:first-child {
    display: flex;
    flex-direction: column;
}

.how-prepare__item {
    display: flex;
    width: 50%;
}

.how-prepare__item:last-child {
    flex-direction: column;
}




.how-prepare__item-accordion {
    cursor: pointer;
    position: relative;

}

.item-accordion__icon {
    transition: transform 0.3s ease;
    position: absolute;
    right: 15px;
    top: 15px;
}

.how-prepare__item-accordion.active .item-accordion__icon {
    transform: rotate(180deg);
}

.item-accordion__content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease, padding 0.3s ease;
    padding-top: 0;
    padding-bottom: 0;
}

.how-prepare__item-accordion span {
    font-family: var(--font-family);
    padding: 24px 0px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---svetlyy);
    display: block;
}

.how-prepare__item-accordion--top span {
    padding: 0px 0px 24px;
}

.how-prepare__item-accordion {
    border-bottom: 1px solid #eee;
}

.how-prepare__item-accordion.active .item-accordion__content {
    max-height: 300px;
}

@media (max-width: 1110px) {
    .how-prepare__items {
        flex-direction: column;
        gap: 32px;
    }

    .how-prepare__item {
        width: 100%;
    }
}

@media (max-width: 800px) {
    .how-prepare {
        padding: 60px 0px;
    }

    .how-prepare__item-title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---svetlyy);
    }

    .how-prepare__item-accordion span {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--tekst---svetlyy);
        padding: 16px 0px;

    }

    .how-prepare__item-accordion p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: rgba(255, 255, 255, 0.8);
    }

    .how-prepare__item-text p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: rgba(255, 255, 255, 0.8);

    }

}