// about-1
.about {
    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        margin-bottom: 40px;
    }

    &__item {
        position: relative;
    }

    &__title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 48px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
        margin-bottom: 40px;
    }

    &__text p:not(:last-child) {
        margin-bottom: 20px;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px;
        overflow: hidden;
    }

    &__btn {
        border-radius: 5px;
        padding: 8px 20px;
        background: var(--belyy);
        position: absolute;
        bottom: 15px;
        left: 15px;

        &:hover {
            opacity: 0.9;
            background: var(--izobrazhenie);
        }
    }

    &__advantages {
        display: flex;
        gap: 20px;
    }

    &__advantage {
        flex: 1 0 20%;
        border-radius: 20px;
        padding: 25px;
        background: #eff0f2;
        min-height: 170px;
        display: flex;
        flex-direction: column;
        gap: 18px;

        & p:first-child {
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: var(--color-zelenyy);

            & span {
                font-weight: 700;
                font-size: 48px;
                line-height: 130%;
                color: var(--color-svetlo-zelenyy-1);
            }
        }

        & p:last-child {
            font-size: 14px;
            color: var(--tekst-svetlyy);
            max-width: 72%;
        }
    }
}

.about__grid {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 10px;
}

.about__img {
    width: 512px;
    height: 411px;
    display: block;
}

.about__item--1 {
    grid-column: 1 / span 2;
}

.about__item--2 {
    grid-column: 3;
    grid-row: 1 / span 2;
}

.about__item--3 {
    grid-column: 1 / span 2;
}

.about__item p:not(:last-child) {
    margin-bottom: 12px;
}

@media (max-width: 1200px) {
    .about__img {
        width: 400px;
    }
}

@media (max-width: 959px) {
    .about__grid {
        display: flex;
        flex-direction: column;
    }

    .about__img {
        width: 100%;
        height: 168px;
    }
}

@media (max-width: 850px) {
    .about {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            margin-bottom: 20px;
        }

        &__title {
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 110%;
            letter-spacing: -0.03em;
            color: var(--tekst---temnyy);
            margin-bottom: 28px;
        }

        &__text p:not(:last-child) {
            margin-bottom: 14px;
        }

        &__btn {
            bottom: 12px;
            left: 12px;
        }

        &__advantages {
            flex-direction: column;
            gap: 10px;
        }

        &__advantage {
            flex: 1 0 20%;
            border-radius: 10px;
            padding: 12px 16px 16px 16px;
            min-height: auto;
            gap: 6px;

            & p:first-child {
                & span {
                    font-size: 24px;
                }
            }

            & p:last-child {
                max-width: 100%;
            }
        }
    }

    .about__grid {
        display: flex;
        grid-template-columns: auto auto auto;
        gap: 10px;
        flex-direction: column;
    }
}

.about__item-btn {
    display: none;
}

@media (max-width: 600px) {
    .intro-1__cards img {
        height: 49px;
    }

    .about__item--3 {
        height: 84px;
        overflow: hidden;
        position: relative;
    }

    .about__item--3.expanded {
        height: auto;
        overflow: visible;
    }

    .about__item-btn {
        display: block;
    }


    .about__item-btn {
        border: 1px solid var(--osnovnoy---500);
        border-radius: 6px;
        width: 100%;
        height: 52px;
        background: var(--tekst---svetlyy);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--osnovnoy---500);
        transition: background 0.2s ease;
        color: background 0.2s ease;
    }

    .about__item-btn:hover {
        background: #d31d4425;
    }

    .about__item-btn::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid #d11039;
        border-right: 1px solid #d11039;
        transform: rotate(135deg);
        margin-bottom: 2px;
        transition: all 0.3s ease;
        margin-left: 8px;
    }

    .expanded.about__item-btn::after {
        content: "";
        display: inline-block;
        width: 8px;
        margin-left: 8px;
        height: 8px;
        border-top: 1px solid #d11039;
        border-right: 1px solid #d11039;
        transform: rotate(315deg);
        margin-bottom: -1px;
        transition: all 0.3s ease;
    }
}

.how-treat {
    padding: 100px 0px;
}

.how-treat__title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 48px;
    line-height: 110%;
    letter-spacing: -0.03em;
    text-align: center;
    color: var(--tekst---temnyy);
    margin-bottom: 60px;
}

.how-treat__items {
    display: flex;
    gap: 20px;
}

.how-treat__item {
    border-radius: 20px;
    padding: 32px;
    width: 335px;
    height: 335px;
    background: #FDF5F7;

}

.how-treat__item-title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    color: #000;
    margin-bottom: 12px;
}

.how-treat__item-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    color: rgba(73, 55, 55, 0.8);
}

.how-treat__item-svg {
    margin-bottom: 28px;
}

@media (max-width: 1370px) {
    .how-treat__wrap {
        overflow-x: auto;
        overflow-y: hidden;
        -ms-overflow-style: none;
        scrollbar-width: none;
    }

    .how-treat__items {
        width: 1370px;
    }
}

@media (max-width: 800px) {
    .types-dependencies__wrap {
        width: 1120px;
    }
}

@media (max-width: 600px) {
    .how-treat {
        padding: 40px 0px;
    }

    .how-treat__items {
        width: 1100px;
    }

    .how-treat__item {
        width: 260px;
    }

    .types-dependencies__wrap {
        width: 1120px;
    }

    .how-treat__item-text {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        color: rgba(0, 0, 0, 0.7);
    }

    .how-treat__item-title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 16px;
        color: #000;
        margin-bottom: 8px;
    }

    .how-treat__title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
        margin-bottom: 28px;
    }
}