/* indications */

.indications__title-wrap-4 {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 50px;
    gap: 20px;

    &>* {
        flex-basis: calc(50% - 10px);
    }
}

.indications__inner-4 {
    display: flex;
    flex-direction: column;
}

.indications__list-4 {
    & ul {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;

        &>* {
            flex-basis: calc(25% - 15px);
        }

        & li {
            padding: 20px 30px 20px 60px;
            position: relative;
            border-radius: 5px;

            &::before {
                left: 20px;
                top: 50%;
                transform: translateY(-50%);
                width: 22px;
                height: 22px;
                content: '';
                position: absolute;
            }
        }
    }
}

.indications__list-positive-4 {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #BEDBD7;
    & ul {
        & li {

            background: var(--gradient-green);
            color: #fff;

            &::before {
                background: url("./../img/help/check-mark-5.svg") no-repeat center center / 100% auto;
            }
        }
    }
}

.indications__list-negative-4 {
    & ul {
        & li {
            background: var(--gradient-light-gray);
            color: var(--tekst);

            &::before {
                background: url("./../img/help/check-mark-8.svg") no-repeat center center / 100% auto;
            }
        }
    }
}

/* indications media */

@media (max-width: 1200px) {
    .indications__list-4 {
        & ul {
            justify-content: center;
            &>* {
                flex-basis: calc(33.333333% - 13.333px);
            }
        }
    }
}

@media (max-width: 1024px) {
    .indications__list-4 {
        & ul {
            justify-content: center;
            &>* {
                flex-basis: calc(50% - 10px);
            }
        }
    }
}

@media (max-width: 950px) {
    .indications__title-wrap-4 {
        margin-bottom: 20px;

        &>* {
            flex-basis: 100%;
        }
    }
}

@media (max-width: 850px) {

    .indications__inner-4 {
        &>* {
            flex-basis: 100%;
        }
    }

}

@media (max-width: 650px) {
    .indications__list-4 {
        & ul {
            gap: 10px;
            justify-content: center;
            &>* {
                flex-basis: 100%;
            }
        }
    }

}