// prices-1
.prices {
    &__flex {
        display: flex;
        gap: 50px;
    }

    &__left {
        display: block;
        flex-basis: 40%;
        margin: 0;

        &_text {
            margin-bottom: 20px;
        }
    }

    &__inner {
        flex-basis: 65%;
    }

    &__title {
        margin-bottom: 30px;
    }

    &__btn {
        display: block;
        margin-bottom: 50px;
        width: fit-content;
    }

    &__sort {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__table {
        display: block;
        width: 100%;

        &_body {
            display: block;
            width: 100%;
        }

        &_item {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 20px;
            color: var(--tekst);
            padding-top: 25px;
            border-bottom: 1px solid #c7d6dc;

            &:first-child {
                border-top: 1px solid #c7d6dc;
            }
        }

        &_name {
            flex: 1 0 80%;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
        }

        &_cell {
            border-right: 1px solid #c7d6dc;
            padding-right: 20px;
        }

        &_btn {
            padding: 8px 16px;
            background: var(--color-svetlo-zelenyy-2);
        }

        &_btn-circle {
            width: 40px;
            height: 40px;
            background: rgba(61, 124, 133, 0.25);
            color: var(--tekst-svetlyy);

            &::after {
                transform: rotate(135deg);
                margin: 0;
            }
        }

        &_info {
            flex-basis: 100%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_description {
            border-radius: 10px;
            padding: 20px 30px;
            background: rgba(234, 237, 242, 0.4);

            & p:first-child {
                font-weight: 500;
                color: var(--tekst);
                margin-bottom: 10px;
            }
        }
    }
}

.accordion--active.prices__table_item {
    padding-bottom: 20px;
}


// media prices-1
@media (max-width: 950px) {
    .prices {
        &__flex {
            flex-direction: column;
            gap: 30px;
        }


        &__sort {
            flex-direction: column;
            gap: 12px;
        }

        &__table {
            &_item {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 16px;
                justify-content: space-between;
                color: var(--tekst);
                padding-top: 30px;
                border-bottom: 1px solid #c7d6dc;
            }

            &_name {
                flex: 1 0 75%;
                order: 1;
            }

            &_cell {
                flex: 1 0 45%;
                order: 3;
            }

            &_cost {
                border: none;
            }

            &_btns {
                display: contents;
            }

            &_btn {
                order: 5;
                width: 100%;
            }

            &_btn-circle {
                order: 2;
            }

            &_info {
                order: 6;
            }
        }
    }

    .accordion--active.prices__table_item {
        padding-bottom: 30px;
    }
}

@media (max-width: 688px) {
    .prices {
        &__left {
            display: contents;

            &_text {
                margin: 0;
            }
        }

        &__btn {
            width: 100%;
        }

        &__table {
            &_name {
                font-size: 16px;
            }

            &_cell {
                padding: 10px;
            }

            &_btn-circle {
                width: 30px;
                height: 30px;
            }

            &_info {
                gap: 15px;
            }

            &_description {
                border-radius: 5px;
                padding: 20px;
            }
        }
    }
}

.select-button {
    position: relative;
    display: inline-block;
    width: 100%;
}

.select-button button {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.select-button svg {
    transition: transform 0.3s ease;
}

.select-button.open svg {
    transform: rotate(180deg);
}

.select-options {
    display: none;
    position: absolute;
    top: 69%;
    left: 0;
    right: 0;
    background-color: #fff;
    border-top: none;
    z-index: 10;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--osnovnoy---500);
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
}

.select-button.open .select-options {
    display: block;
}

.option {
    padding: 10px;
    cursor: pointer;
}

.option:hover {
    background-color: #f5f5f5;
}

.prices-page__item {
    display: none;
}

.prices-page__item.selected {
    display: block;
}

#category-button {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--osnovnoy---500);
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
    padding: 20px;
    width: 335px;
    height: 52px;
    margin-bottom: 40px;
}

@media (max-width: 800px) {
    #category-button {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--osnovnoy---500);
        border: 1px solid var(--osnovnoy---500);
        border-radius: 6px;
        padding: 20px;
        width: 100%;
        height: 52px;
    }
}