/* header  */

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
}

main {
    margin-top: 0px;
    transition: margin-top 0.3s ease;
}

main.shifted {
    margin-top: 236px;
}

.header__logo {
    display: block;
    overflow: hidden;
    max-width: 210px;
    max-height: 45px;
    min-width: 210px;
    min-height: 45px;
    width: 100%;
}

.logo__picture {
    width: 100%;
    height: 100%;

    & img {
        width: 100%;
        height: 100%;
    }
}

.header__plashka,
.header__main,
.header__top {
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}

.header__plashka {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    text-align: center;
    color: var(--tekst---svetlyy);
    background: var(--osnovnoy---500);

}

.header__plashka a {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    text-align: center;
    color: var(--tekst---svetlyy);
    background: var(--osnovnoy---500);

}

.header__plashka_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    padding: 10px;
    margin: 0 auto;
}

.header__plashka.hide,
.header__top.hide {
    padding: 0;
}



/* header__top  */

.header__top {
    padding: 13px 0 28px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    transition: all 0.3s ease;
}

.header__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}

.header__info_item {

    gap: 10px;
}

.header-mobile__wrapper .header__info_phone p:first-child,
.header-mobile__wrapper .header__info_email p:first-child,
.header-mobile__wrapper .header__info_mode p:first-child,
.header-mobile__wrapper .header__info_social p:first-child,
.header-mobile__wrapper .header__info_location p:first-child {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: rgba(22, 22, 22, 0.4);
}

.header-mobile__wrapper .header__info_phone a:link {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    color: #454545;
}

.header-mobile__wrapper .header__info_social p:first-child {
    margin-bottom: 8px;
}

.header-mobile__wrapper .header__info_mode p:last-child {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.header-mobile__wrapper .header__info_email a:link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #454545;
}

.header__info_icon {
    border-radius: 5px;
    width: 40px;
    height: 40px;
    padding: 8px;
    background: var(--color-svetlo-zelenyy-2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__info_text {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
}

.header__info_text div p:first-child {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: rgba(23, 23, 23, 0.55);
}

.header__info_text div p:last-child {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: #171717;
}

.header__info_location--mode {
    display: flex;
}


.header__info_location--mode div:first-child {
    padding-top: 2px;
    margin-right: 8px;
}

.header__info_location {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    color: #171717;
}

.header__info_location p:last-child {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    color: rgba(23, 23, 23, 0.55);
}

.header__info_location button {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #d11039;
}

.header__info_location button:not(:last-child) {
    margin-right: 8px;
}

.header__info_phone a {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 125%;
    text-transform: uppercase;
    color: #171717;
    display: flex;
    align-items: center;
}

.header__info_phone a svg {
    margin-right: 8px;
}

.header__info_phone a:hover,
.header__info_location button:hover {
    text-decoration: underline;
}

.header__networks {
    display: flex;
    gap: 6px;
}

.header__network {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    background: var(--color-temno-zelenyy);
    transition: all 0.3s ease;
}

.header__network:hover {
    background: var(--color-svetlo-zelenyy-1);

    svg path {
        fill: var(--color-temno-zelenyy);
    }
}


.header__search {
    padding: 0;
    width: 46px;
    height: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__search:hover {
    svg path {
        stroke: var(--belyy);
    }
}




/* header__main */
.header__main {
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 28px;
}


.header__menu {
    display: contents;
}

.header__btns {
    display: flex;
    gap: 10px;
}

.header__nav>ul {
    display: flex;
    align-items: center;
    gap: 36px;
}

.header__nav>ul>li>a,
.header__nav>ul>li>button {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: #171717;

    &:hover {
        font-family: var(--font-family);
        font-size: 16px;
        line-height: 125%;
        color: var(--osnovnoy---500);
    }
}

.header__nav_item.hide-item--active>button {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 125%;
    color: var(--osnovnoy---500);
}


.header__nav>ul>li>a:hover,
.header__nav>ul>li>button span {
    position: relative;
}

.header__nav>ul>li>a:hover,
.header__nav>ul>li>button span:after {
    display: block;
    content: "";
    width: 8px;
    height: 6px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    background: url("./../img/alco/icons/vec.svg") center center no-repeat;
    -webkit-transition: -webkit-transform 0.2s ease;
    transition: -webkit-transform 0.2s ease;
    -o-transition: transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
    position: absolute;
    left: auto;
    bottom: auto;
    right: -13px;
    top: 8px;
}

.header__nav_item.hide-item--active>button span:after {
    transform: rotate(180deg);
}

.header__nav_about>button {
    display: flex;
    align-items: center;
    gap: 10px;
}

.header__nav_service>button {
    border-radius: 5px;
    padding: 10px;
    background: var(--fon-plashek);
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;

    &:hover {
        background: var(--color-zelenyy);
        color: var(--belyy);

        .header__nav_service-span span {
            background: var(--belyy);
        }
    }
}

.header__nav_service-span {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 17px;
    height: 20px;
}

.header__nav_service-span>span {
    border-radius: 5px;
    width: 16px;
    height: 2px;
    background: var(--color-zelenyy);
    transition: all 0.3s ease;
}




/* header__discount */
.header__discount {
    padding: 12px 0;
    background: var(--color-temno-zelenyy);
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: var(--belyy);
    transition: all 0.3s ease;
    opacity: 1;
    max-height: 500px;
}

.header__discount.hidden {
    opacity: 0;
    max-height: 0;
    padding: 0;
    overflow: hidden;
}

.header__discount p span:last-child {
    display: inline-block;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: var(--belyy);
    padding: 5px 10px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    text-wrap: nowrap;
}

.header__discount_text {
    display: flex;
    align-items: center;
    gap: 10px;
    text-align: left;
}

.header__discount_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}

.header__discount_more {
    position: relative;
    font-weight: 500;
    font-size: 14px;
    color: var(--belyy);
    padding-right: 15px;
    margin-right: 15px;
}

.header__discount_close {
    position: relative;
    font-weight: 400;
    font-size: 14px;
    color: var(--belyy);
    opacity: 0.6;
    padding: 0 15px;
    border-left: 1px solid var(--belyy);
}

.header__discount_more:before,
.header__discount_more:after,
.header__discount_close:before,
.header__discount_close:after {
    content: "";
    position: absolute;
    width: 7px;
    height: 1px;
    background: var(--belyy);
    top: 50%;
    transform: translate(0, -50%);
    right: 0;
}

.header__discount_more:before {
    transform: rotate(45deg);
    top: calc(50% - 2px);
}

.header__discount_more:after {
    transform: rotate(-45deg);
    top: calc(50% + 2px);
}

.header__discount_close:before {
    width: 10px;
    transform: rotate(45deg);
}

.header__discount_close:after {
    width: 10px;
    transform: rotate(-45deg);
}

.header__discount_more:hover,
.header__discount_close:hover {
    opacity: 0.5;
}




/*  header__submenu */
.header__submenu {
    display: none;
    z-index: 100;
}

.hide-item {
    position: relative;
}

.hide-item--active {
    .header__submenu {
        display: block;
    }
}

.hide-item--active.header__nav_about>button {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    color: var(--osnovnoy---500);

    &::after {
        width: 8px;
        height: 8px;
        opacity: 0.5;
    }
}

.hide-item--active.header__nav_service>button {
    background: var(--plashki);
    color: var(--belyy);

    .header__nav_service-span span {
        background: var(--belyy);
    }

    .header__nav_service-span span:first-child {
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: 7px;
    }

    .header__nav_service-span span:nth-child(2) {
        opacity: 0;
    }

    .header__nav_service-span span:last-child {
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
        position: relative;
        bottom: 7px;
    }
}





/* mobile__wrapper */
.header-mobile__wrapper {
    display: none;
    width: 100%;

    &-phone {
        order: 0;
    }
}

.header-mobile__burger-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
}

.header-mobile__burger-btns button {
    display: flex;
}

.header-mobile__eye {
    order: 6;
    text-align: right;
    margin-top: -45px;

    & button {
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.6;
        }
    }
}

.header__btn--question {
    border: 1px solid var(--osnovnoy---500);
    border-radius: 6px;
    padding: 10px 8px;
    width: 164px;
    height: 45px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--osnovnoy---500);
}

.header__btn--question:hover {
    background: var(--osnovnoy---500);
    color: var(--tekst---svetlyy);
    border: 1px solid var(--osnovnoy---500);
}

.header__btn--services {
    border-radius: 6px;
    padding: 10px 8px;
    width: 163px;
    height: 45px;
    background: var(--osnovnoy---500);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---svetlyy);
}

.header-mobile__info {
    order: 5;
}

.header-mobile__phone {
    display: flex;
    justify-content: center;
    width: 100%;
    gap: 16px;

    & a {
        font-weight: 700;
        font-size: 18px;
        line-height: 130%;
        color: var(--color-zelenyy);
        padding-right: 16px;
        border-right: 1px solid var(--tegi);
        display: flex;
        align-items: center;
        gap: 6px;
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.6;
        }
    }

    & img {
        display: block;
        width: 20px;
        height: 20px;
        padding: 4px;
        border-radius: 5px;
        background: var(--fon-plashek);
    }

    & p {
        font-size: 12px;
        color: var(--tegi);
    }
}


/* burger__menu */
.burger__menu {
    width: 36px;
    height: 36px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    background: var(--osnovnoy---500);
}

.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.hamRotate.active {
    transform: rotate(45deg);
}

.hamRotate180.active {
    transform: rotate(180deg);
}

.header-mobile__btns {
    display: none;
}

.header-mobile__btns .header__btn {
    width: 146px;
    height: 38px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.01em;

}

.line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: white;
    stroke-width: 5.5;
    stroke-linecap: round;
}

.header-mobile__burger-btns a {
    display: flex;
}

.ham6 .top {
    stroke-dasharray: 40 172;
}

.ham6 .middle {
    stroke-dasharray: 21 111;
}

.ham6.active .middle {
    stroke-dasharray: 39 111;
}

.ham6 .bottom {
    stroke-dasharray: 40 172;
}

.ham6.active .top {
    stroke-dashoffset: -132px;
}

.ham6.active .middle {
    stroke-dashoffset: -71px;
}

.ham6.active .bottom {
    stroke-dashoffset: -132px;
}

.header {
    position: sticky;
}

// media header-1
@media (max-width: 1380px) {
    .header__info_phone {
        display: none;
    }
}

@media (max-width: 1260px) {
    main {
        margin-top: 0px;
    }

    .header__nav>ul>li>a:hover,
    .header__nav>ul>li>button span {
        position: unset;
    }

    .header-mobile__btns {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
    }

    .header__plashka {
        border-radius: 0 0 20px 20px;
    }

    main.shifted {
        margin-top: 172px;
    }

    .header-mobile__wrapper {
        display: block;
    }

    .header__plashka {
        font-size: 12px;

        &_row {
            padding: 8px 0;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;

            & p {
                text-align: center;
            }
        }

        &_btns {
            display: none;
        }
    }

    .header__plashka a {
        font-size: 12px;
    }

    .header__discount {
        position: relative;

        & p {
            font-size: 12px;

            &::before {
                display: none;
            }
        }

        &_row {
            width: 100%;
            -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
            gap: 5px;
        }

        &_more,
        &_close span {
            display: none;
        }

        &_btns {
            -ms-flex-preferred-size: 0;
            flex-basis: 0;
        }

        &_close {
            position: absolute;
            top: 12px;
            right: 8px;
            opacity: 1;

            &::before,
            &::after {
                width: 15px;
            }
        }
    }


    .header__info,
    .header__links,
    .header__nav,
    .header__btns,
    .header__networks {
        display: none;
    }

    .header__logo {
        display: block;
        overflow: hidden;
        max-width: 149px;
        max-height: 32px;
        min-width: 149px;
        min-height: 32px;
        width: 100%;
    }

    .header__main {
        padding: 20px 0 12px;
        gap: 20px;
    }

    .header__top {
        padding: 10px 0 10px;
        border: none;
    }

    .header__menu {
        display: none;
    }

    .header__menu--active {
        position: fixed;
        top: 138px;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100% - 138px);
        overflow-y: auto;
        padding: 10px 20px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 30px;

        &::-webkit-scrollbar {
            width: 3px;
        }

        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--color-temno-zelenyy);
        }

        & .header__top {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            justify-content: flex-start;
            gap: 20px;
            padding: 20px;
        }

        & .header__nav {
            display: block;
            opacity: 1 !important;
            order: 1;
        }

        & .header__btns {
            display: flex;
            order: 2;

            & button {
                flex: 1 0 auto;
            }
        }

        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 16px;
            order: 3;
            padding-bottom: 30px;
            border-bottom: 1px solid var(--color-temno-zelenyy);
            margin-bottom: 30px;
        }

        & .header__links {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
            order: 4;
        }

        & .header__networks {
            display: flex;
        }
    }

    .header__info_phone {
        display: block;
    }

    .header__info_location button {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #454545;
        text-decoration: none;
    }

    .header__nav {
        display: none;

        &>ul {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            -webkit-box-align: stretch;
            -ms-flex-align: stretch;
            align-items: stretch;
            gap: 0;
            margin-bottom: 0;
            position: relative;

            &>li>a {
                display: block;
                font-weight: 500;
                padding: 9px 0;
            }
        }
    }

    .header__nav_about {
        & button {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            gap: 10px;
            width: 100%;
            font-weight: 500;
        }
    }

    .header__nav_service button {
        width: 100%;
        justify-content: center;
    }

    .header__nav_list {
        position: relative;
        z-index: 10;
    }
}

@media (max-width: 800px) {
    main {
        margin-top: 0px;
    }

    main.shifted {
        margin-top: 170px;
    }

    .header__menu--active {
        top: 124px;
        height: calc(100% - 124px);
    }

    .header-mobile__phone {
        & a {
            text-wrap: nowrap;
            flex: 1 0 48%;
            font-size: 14px;
        }

        & p {
            flex: 1 0 35%;
            font-size: 12px;
        }
    }

    .header__discount p {
        font-size: 12px;
        text-align: left;
    }

    .header__info {
        &_item {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            gap: 12px;
        }

        &_icon {
            min-width: 20px;
            width: 20px;
            height: 20px;
            padding: 4px;
        }

        &_text {
            font-size: 12px;

            & p:last-child {
                font-size: 14px;
            }
        }

        &_phone a {
            font-size: 16px;
        }
    }
}

@media (max-width: 540px) {
    main {
        margin-top: 0px;
    }

    main.shifted {
        margin-top: 186px;
    }

    .header-mobile__phone {
        justify-content: space-between;

        & a {
            flex: 1 0 auto;
        }
    }

    .header__btns {
        flex-direction: column;
    }
}

