// faq-1
.faq {
    &__grid {
        display: grid;
        grid-template-columns: 6.2fr 3.2fr;
        gap: 60px;
    }

    &__list li {
        margin-bottom: 20px;
    }

    &__button {
        padding-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        text-align: left;
        line-height: 140%;
        padding-bottom: 20px;
        border-bottom: 1px solid #eee;
    }

    &__button.active {
        & .faq__content {
            max-height: 600px;
            padding-top: 20px;
        }

        & .faq__btn-more {
            & svg path:last-child {
                transform: rotateX(90deg);
            }
        }
    }

    &__name {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        flex-basis: 80%;
    }

    &__content {
        flex-basis: 100%;
        max-height: 0;
        overflow: hidden;

        transition: all 0.3s ease;
        box-sizing: border-box;
    }

    &__btn-more {
        display: block;
        width: 34px;
        height: 34px;
        border: 1px solid #eee;
        border-radius: 20px;
        background: #D31D44;

        & svg path {
            transition: all 0.3s ease;
        }
    }

    &__editor {
        border-radius: 10px;
        padding: 20px;
        background: var(--color-svetlo-zelenyy-2);
        display: flex;
        margin-top: 30px;

        &_left {
            display: flex;
            gap: 16px;
            align-items: center;
            padding-right: 30px;
            border-right: 1px solid #eee;
            ;
            margin-right: 30px;
            flex-basis: 50%;
        }

        &_img {
            display: block;
            border-radius: 100px;
            min-width: 68px;
            width: 68px;
            height: 68px;
            overflow: hidden;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            font-size: 14px;
            line-height: 150%;
            color: var(--tekst-svetlyy);
        }

        &_fio {
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: var(--tekst);
        }

        &_right {
            display: flex;
            flex-direction: column;
            gap: 4px;
            justify-content: space-between;
        }

        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--tekst);
        }
    }


    &__form {
        border-radius: 20px;
        padding: 40px;
        background: rgba(234, 237, 242, 0.7);

        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 120%;
            color: var(--tekst);
            margin-bottom: 12px;
        }

        &_subtitle {
            color: var(--tekst-svetlyy);
            margin-bottom: 40px;
        }

        &_inputs {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
    }
}

.faq__button .faq__name {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: rgba(22, 22, 22, 0.8);
}

.faq__content P {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
}

.general__title-h1--faq {
    max-width: 1200px;
}

.faq__button.active .faq__name {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--osnovnoy---500);
}

.faq__button.active .faq__btn-more svg path {
    fill: #D31D44;
}

.faq__button.active .faq__btn-more {
    background-color: rgba(211, 29, 68, 0.15);
}

// media faq-1
@media (max-width: 1150px) {
    .faq {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 30px;
            display: contents;
        }

        &__block_form {
            order: 5;
        }
    }

    .faq__block_photo {
        display: none;
    }
}

@media (max-width: 688px) {
    .faq {
        &__button.active {
            & .faq__content {
                max-height: 1000px;
            }
        }

        &__name {
            font-weight: 700;
            font-size: 16px;
            color: var(--tekst);
        }

        &__btn-more {
            width: 28px;
            height: 28px;
        }

        &__editor {
            flex-direction: column;
            margin-top: 20px;

            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--izobrazhenie);
                margin-bottom: 16px;
            }
        }

        &__form {
            border-radius: 10px;
            padding: 20px;

            &_title {
                font-size: 16px;
            }

            &_subtitle {
                margin-bottom: 16px;
            }
        }

        &__btn {
            margin-bottom: 10px;
        }
    }

    .faq__button .faq__name {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: rgba(22, 22, 22, 0.8);
    }

    .faq__button.active .faq__name {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--osnovnoy---500);
    }

    .faq__content P {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--tekst---seryy);
    }

    .faq__button {
        padding-top: 16px;
        padding-bottom: 16px;
    }

    .faq__list li {
        margin-bottom: 0px;
    }
}