// blockquote-1
.blockquote-1 {
    background: #f2f8f7;
    border-left: 3px solid var(--color-temno-zelenyy);
    padding: 30px 30px 30px 40px;
    font-size: 18px;
    font-style: italic;
    line-height: 140%;
    color: var(--tekst-svetlyy);

    &__editor {
        padding-top: 20px;
        border-top: 1px solid var(--izobrazhenie);
        margin-top: 20px;
        display: flex;
        align-items: center;
        font-style: normal;

        &_left {
            display: flex;
            gap: 16px;
            align-items: center;
            padding-right: 40px;
            border-right: 1px solid var(--izobrazhenie);
            margin-right: 40px;
        }
    
        &_img {
            display: block;
            border-radius: 100px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &_text {
            display: flex;
            flex-direction: column;
            gap: 4px;
            & p {
                font-size: 14px;
                line-height: 150%;
                margin-bottom: 0 !important;
                &:last-child {
                    font-weight: 500;
                    font-size: 18px;
                    color: var(--tekst);
                }
            }
        }
    
        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--tegi);
        }
    }
}


@media (max-width: 688px) {
    .blockquote-1 {
        padding: 20px;
        font-size: 14px;
        border-left: none;
        border-top: 3px solid var(--color-temno-zelenyy);
        &__editor {
            flex-direction: column;
            align-items: stretch;
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 14px;
                border-bottom: 1px solid var(--izobrazhenie);
                margin-bottom: 14px;
            }
            &_img {
                min-width: 60px;
                width: 60px;
                height: 60px;
            }
            &_text {
                gap: 2px;
                & p {
                    font-size: 12px;
                    &:last-child {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
