// blockquote-3
.blockquote-3 {
    padding-top: 25px;
    border-top: 1px solid #c7d6dc;
    
    &__main {
        display: flex;
        flex-direction: column;
        align-items: start;
        gap: 25px;
        font-size: 18px;
        line-height: 140%;
        font-style: italic;
        color: var(--color-temno-zelenyy);
    }
    
    &__editor {
        padding-top: 25px;
        border-top: 1px solid #c7d6dc;
        margin-top: 25px;
        display: flex;
        align-items: center;
        font-style: normal;
    
        &_left {
            display: flex;
            gap: 20px;
            align-items: center;
            padding-right: 30px;
            border-right: 1px solid var(--izobrazhenie);
            margin-right: 30px;
        }
    
        &_img {
            display: block;
            border-radius: 100px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &_text {
            display: flex;
            flex-direction: column;
            gap: 4px;
            & p {
                font-size: 14px;
                line-height: 150%;
                margin-bottom: 0 !important;
                &:last-child {
                    font-weight: 500;
                    font-size: 16px;
                    color: var(--tekst);
                }
            }
        }

        &_right {
            display: flex;
            flex-direction: column;
            gap: 8px;
        }
    
        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--tegi);
        }
    }
}


@media (max-width: 688px) {
    .blockquote-3 {
        padding: 27px 0 30px 0;
        margin-top: 15px;
        position: relative;
        &__main {
            font-size: 14px;
            font-style: normal;
            &_bg {
                position: absolute;
                top: -15px;
                left: 0;
                width: 46px;
                height: 30px;
                padding-right: 16px;
                background-color: #fff;
            }
        }
        &__editor {
            margin-top: 24px;
            padding-top: 0;
            border: none;
            flex-direction: column;
            align-items: stretch;
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--izobrazhenie);
                margin-bottom: 16px;
                gap: 12px;
            }
            &_img {
                min-width: 60px;
                width: 60px;
                height: 60px;
            }
            &_right {
                gap: 4px;
            }
            &_text {
                gap: 2px;
                & p {
                    font-size: 12px;
                    &:last-child {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
