.popup-feedback {
    &__content-2 {
        background: transparent;
        display: flex;
        max-width: 1320px;
        background: var(--bg-svetlo-seryy);
        padding: 40px;
        border-radius: 15px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
    }

    &__title-2 {
        font-weight: 700;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 8px;
    }

    &__subtitle-2 {
        font-size: 16px;
        line-height: 150%;
        color: var(--tekst-svetlyy);
        margin-bottom: 30px;

    }

    &__form-2 {
        display: flex;
        flex-direction: column;
        gap: 15px
    }

    &__input-2 {
        background-color: var(--fon-plashek);
    }

    &__input-2:hover {
        background-color: var(--belyy);
        border-color: var(--fon-plashek);
    }

    &__input-2:placeholder { 
        color: var(--tegi);
    }

    &__close-2 {
        width: 40px;
        height: 40px;
        top: 0;
        right: -50px;
    }
}

.popup-feedback__btn-2 {
    min-width: fit-content;
}

.popup-feedback__form-input__wrap-2 .select {
    padding: 16px;
    background-color: var(--fon-plashek);
    color: var(--tegi);
}

.popup-feedback__inner-2 {
    display: flex;
    gap: 70px;
    align-items: stretch;
}

.popup-feedback__wrap-2 {
    display: flex;
    flex-direction: column;
    flex-basis: 50%;
}

.popup-feedback__bottom-wrap-2 {
    display: flex;
    align-items: center;
    gap: 15px;
    padding-top: 15px;
    border-top: 1px solid var(--color-svetlo-zelenyy-2);
}

.popup-feedback__picture-2 {
    height: 100%;
    flex-basis: 50%;

    & img {
        height: 100%;
        width: 100%;
        border-radius: 15px;
        object-fit: cover;
    }
}

.popup-feedback__stars-2 {
    display: flex;
    align-items: center;
    gap: 15px;
    background-color: var(--fon-plashek);
    justify-content: space-between;
    padding: 15px;
    border-radius: 10px;

    & p {
        color: var(--tegi);
        font-size: 14px;
    }

}

.popup-feedback__textarea-2 {
    background: var(--fon-plashek);
    border: 1px solid transparent;
    border-radius: 5px;
    color: var(--tegi);
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    padding: 16px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    resize: none;
    height: 140px;
}

.popup-feedback__textarea-2:placeholder {
    color: var(--tegi);
}

.popup-feedback__textarea-2:hover {
    background: var(--belyy);
    border-color: var(--fon-plashek);
}

.popup-feedback__textarea-2:focus {
    background: var(--belyy);
    border: 1px solid var(--tegi);
    outline: none;
}

.popup-feedback__form-input__wrap-2 {
    display: flex;
    gap: 15px;
    font-size: 14px;

    &>* {
        flex-basis: calc(50% - 7.5px);
    }
}


.feedback-full-stars {
    text-align: center;
    align-items: center;
}

.feedback-full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex
}

.feedback-full-stars input {
    left: -9999px;
    position: absolute
}

.feedback-full-stars label {
    cursor: pointer;
    margin: 0
}

.feedback-full-stars label svg {
    fill: var(--color-zelenyy);
    height: 18px;
    margin: 0 2px;
    -webkit-transition: fill .3s;
    transition: fill .3s;
    width: 20px
}

.feedback-full-stars input:checked~label svg {
    fill: transparent
}

.feedback-full-stars .rating-group:hover label svg {
    fill: var(--color-zelenyy)
}

.feedback-full-stars .rating-group input:hover~label svg {
    fill: transparent
}

.popup-feedback-response {
    &__content {
        max-width: 606px;
        padding: 10px;
        display: flex;
        flex-direction: column;
    }

    &__top {
        border-radius: 10px;
        padding: 30px 10px;
        background: var(--belyy);
        text-align: center;
        margin-bottom: 30px;

        & p {
            margin: 0;
        }
    }

    &__inner {
        padding: 0 30px 30px 30px;
    }

    &__main {
        padding: 0 15px;
        padding-bottom: 30px;
        border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
        margin-bottom: 30px;
        text-align: center;

        & img {
            margin: 0 auto;
            margin-bottom: 20px;
        }

        & p {
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_error {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
    }

    &__btn {
        display: block;
        width: 100%;
    }
}


/* select */
.popup-feedback__select {
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    padding: 16px 20px;
    background: var(--fon-plashek);
    opacity: 0.7;
    font-size: 14px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.popup-feedback__select:placeholder {
    color: var(--tegi);
    font-size: 14px;
}
.popup-feedback__select:hover {
    opacity: 1;
}

.popup-feedback__select:focus {
    outline: 1px solid var(--color-zelenyy);
}


@media (max-width: 1500px) {
    .popup-feedback__close-2 {
        right: 20px;
        top: 20px;
    }

    .popup-feedback__inner-2 {
        gap: 20px;
    }
}

@media (max-width: 924px) {
    .popup-feedback__inner-2 {
        flex-direction: column-reverse;
        width: 100%;
    }


    .popup-feedback__close-2 {
        right: 10px;
        top: 10px;
        border: 1px solid var(--fon-plashek);
        border-radius: 50%
    }

    .popup-feedback__wrap-2 {
        flex-basis: 100%;
    }

    .popup-feedback__picture-2 {
        flex-basis: 100%;
        max-height: 400px;
        width: 100%;

        & img {
            object-position: top;
        }
    }


    .popup-feedback__title-2 {
        font-size: 30px;
    }
}

@media (max-width: 650px) {
    .popup-feedback__form-input__wrap-2 {
        flex-direction: column;
    }

    .popup-feedback__bottom-wrap-2 {
        flex-direction: column;
    }

    .popup-feedback__btn-2 {
        min-width: 100%;
    }

    .popup-feedback__content-2 {
        padding: 40px 20px 20px 20px;
        border-radius: 10px;
    }

    .popup-feedback__subtitle-2 {
        font-size: 14px;
    }

    .popup-feedback__picture-2 {
        border-radius: 10px;
    }
}

@media (max-width: 550px) {

    .popup-feedback__form-input__wrap-2,
    .popup-feedback__form-2,
    .popup-feedback__bottom-wrap-2 {
        gap: 10px;
    }

    .popup-feedback__inner-2 {
        gap: 10px;
    }

    .popup-feedback__stars-2 {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
    }
    
    .popup-feedback__subtitle-2 {
        margin-bottom: 10px;
    }
}

@media (max-width: 450px) {
    .popup-feedback__picture-2 {
        max-height: 240px;
    }

    .popup-feedback__title-2 {
        font-size: 24px;
    }

    .popup-feedback__close-2 {
        right: 10px;
        top: 10px;
        width: 30px;
        height: 30px;
    }
}