// blockquote-5
.blockquote-5 {
    border: 1px solid var(--color-svetlo-zelenyy-1);
    border-radius: 10px;
    padding: 30px;
    background: rgba(190, 219, 215, 0.05);
    color: var(--color-zelenyy);

    &__editor {
        border-radius: 10px;
        padding: 20px 65px;
        background: var(--color-svetlo-zelenyy-2);
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &::after, &::before {
            content: url("./../img/text-block/text-block-quote.svg");
            position: absolute;
            top: 30px;
            bottom:  30px;
        }
        &::after {
            left: 10px;
        }
        &::before {
            right: 10px;
        }
    
        &_left {
            display: flex;
            gap: 20px;
            align-items: center;
            padding-right: 30px;
            border-right: 1px solid  var(--color-svetlo-zelenyy-1);
            margin-right: 30px;
            position: relative;
            z-index: 2;
        }
    
        &_img {
            display: block;
            border-radius: 100px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &_text {
            display: flex;
            flex-direction: column;
            gap: 4px;
            & p {
                font-size: 14px;
                line-height: 150%;
                margin-bottom: 0 !important;
                &:last-child {
                    font-weight: 500;
                    font-size: 16px;
                    color: var(--tekst);
                }
            }
        }

        &_right {
            display: flex;
            flex-direction: column;
            gap: 8px;
            position: relative;
            z-index: 2;
        }
    
        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--tegi);
        }
    }
}


@media (max-width: 688px) {
    .blockquote-5 {
        padding: 20px;
        &__editor {
            padding: 20px;
            flex-direction: column;
            align-items: stretch;
            &::after, &::before {
                display: none;
            }
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 14px;
                border-bottom: 1px solid #c7d6dc;
                margin-bottom: 14px;
                gap: 12px;
            }
            &_right {
                gap: 4px;
            }
            &_text {
                gap: 2px;
                & p {
                    font-size: 12px;
                    &:last-child {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
