
// page-reviews-3
.page-reviews {
    &__sort {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 24px;

        &_block {
            display: flex;
            gap: 12px;
            width: 80%;
            & .sort_block {
                max-width: 436px;
                width: 100%;
            }
        }
    }

    &__quantity {
        font-size: 14px;
        text-align: right;
        margin-bottom: 20px;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        border: 1px solid var(--color-svetlo-zelenyy-1);
        border-radius: 10px;
        padding: 40px;
        background: var(--belyy);

        &_label {
            display: flex;
            flex-wrap: wrap;
            gap: 4px;
            margin-bottom: 18px;
            & p {
                border-radius: 5px;
                padding: 6px 12px;
                background: var(--color-svetlo-zelenyy-2);
                font-size: 14px;
                color: var(--color-temno-zelenyy);
            }
        }

        &_top {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            padding-bottom: 18px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            margin-bottom: 18px;
        }

        &_author {
            font-size: 14px;
            & p:first-child {
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst);
            }
        }

        &_stars {
            display: flex;
            gap: 2px;
        }
        &_star-active svg path {
            fill: var(--color-temno-zelenyy);
        }

        &_main {
            padding-bottom: 20px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            margin-bottom: 20px;
        }

        &_text {
            margin-bottom: 14px;
            max-height: 176px;
            overflow: hidden;
        }

        &_more::after {
            transform: rotate(135deg);
            margin-bottom: 2px;
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_publication {
            display: flex;
            align-items: center;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--color-zelenyy);
        }

        &_source {
            display: block;
            max-width: 77px;
        }
    }
}


// media page-reviews-3
@media (max-width: 950px) {
    .page-reviews {
        &__inner {
            display: flex;
            flex-direction: column;
        }
        &__sort {
            display: contents;
            &_block {
                width: 100%;
                margin-bottom: 30px;
            }
        }
        &__quantity {
            text-align: left;
            margin-bottom: 40px;
        }
        &__btn {
            order: 3;
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 700px) {
    .page-reviews {
        &__sort {
            &_block {
                flex-direction: column;
                & .sort_block {
                    max-width: none;
                }
            }
        }
        &__cards {
            gap: 12px;
        }
        &__card {
            border-radius: 10px;
            padding: 20px;
            &_label {
                left: 25px;
                flex-wrap: wrap;
                width: fit-content;
            }
            &_top {
                flex-direction: column;
                gap: 10px;
            }
            &_author {
                & p:first-child {
                    font-size: 16px;
                }
            }
            &_bottom {
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
            }
            &_text {
                margin-bottom: 14px;
                max-height: 274px;
            }
        }
    }
}
