// form-7
.form-7 {
    &__row {
        display: flex;
    }

    &__content {
        flex-basis: 60%;
        border-radius: 20px;
        padding: 40px;
        background: var(--color-zelenyy);
        display: flex;
        flex-direction: column;
        gap: 20px;
        justify-content: space-between;
    }

    &__title {
        font-weight: 400;
        font-size: 34px;
        line-height: 140%;
        color: var(--belyy);
    }

    &__text {
        display: flex;
        gap: 30px;
        & p {
            color: var(--belyy);
            padding-right: 30px;
            border-right: 1px solid var(--color-svetlo-zelenyy-2);
        }
    }

    &__inner {
        flex-basis: 40%;
        border-radius: 20px;
        padding: 40px;
        background: var(--color-svetlo-zelenyy-1);
        & p:first-child {
            margin-bottom: 30px;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
}

// media form-7
@media (max-width: 1060px) {
    .form-7 {
        &__row {
            flex-direction: column;
        }
    }
}
@media (max-width: 688px) {
    .form-7 {
        &__content {
            padding: 20px;
        }
        &__title {
            font-size: 24px;
        }
        &__text {
            flex-direction: column;
            gap: 14px;
            & p {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            }
        }
        &__inner {
            padding: 20px;
            & p:first-child {
                margin-bottom: 20px;
            }
        }
    }
}
@media (max-width: 500px) {
    .form-7 {
        &__phone-btn {
            width: 100%;
            justify-content: center;
        }
    }
}