// legal-docs-3
.legal-docs {
    &__inner {
        display: flex;
        gap: 40px;
    }

    &__left {
        flex: 1 0 20%;
        & p {
            margin-bottom: 20px;
        }
        & a {
            display: block;
            width: fit-content;
        }
    }

    &__list {
        flex: 1 0 70%;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        border-radius: 10px;
        padding: 20px 30px;
        background: var(--bg-svetlo-seryy);
        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 12px;
            & p {
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst);
            }
        }

        &_link {
            border-radius: 5px;
            padding: 10px 18px;
            background: var(--color-svetlo-zelenyy-2);
            font-size: 14px;
            color: var(--color-zelenyy);
            display: flex;
            align-items: center;
            gap: 10px;
            flex: 0 0 auto;
        }

        &_info {
            display: flex;
            gap: 100px;
            font-size: 14px;
            & span {
                color: var(--tekst);
            }
        }
    }
}



// media legal-docs-3
@media (max-width: 1000px) {
    .legal-docs {
        &__inner {
            flex-direction: column;
            gap: 30px;
        }
    }
}
@media (max-width: 688px) {
    .legal-docs {
        &__left {
            & a {
                width: 100%;
            }
        }
        &__item {
            padding: 18px 20px;
            &_top {
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
                margin-bottom: 32px;
                & p {
                    font-size: 16px;
                }
            }
            &_link {
                justify-content: center;
                flex: 1 0 auto;
            }
            &_info {
                flex-direction: column;
                gap: 8px;
            }
        }
    }
}