// articles-1
.articles {
    &__flex {
        display: flex;
        align-items: flex-start;
        gap: 50px;
    }

    &__top {
        flex: 1 0 30%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 40px;
        padding-right: 40px;
    }

    &__inner {
        flex: 1 0 60%;
    }

    &__card {
        padding-top: 20px;
        border-top: 1px solid #c7d6dc;
        margin-bottom: 40px;

        &_top {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 20px;
        }

        &_name {
            flex: 1 0 60%;
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
        }

        &_date {
            font-size: 14px;
            color: rgba(64, 95, 106, 0.7);
        }

        &_main {
            flex: 1 0 60%;
            display: flex;
            gap: 30px;
        }

        &_labels {
            display: flex;
            gap: 4px;
            margin-bottom: 20px;
            & p {
                border-radius: 5px;
                padding: 8px 16px;
                background: var(--bg-svetlo-seryy);
                font-size: 12px;
                line-height: 140%;
                text-align: center;
                color: var(--color-temno-zelenyy);
            }
        }

        &_description {
            color: var(--tekst);
            margin-bottom: 20px;
        }

        &_btn {
            border-radius: 5px;
            padding: 10px 20px;
            background: var(--color-svetlo-zelenyy-2);
            width: fit-content;
        }

        &_photo {
            flex: 1 0 33%;
            & img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
            }
        }
    }
}


// media articles-1
@media (max-width: 1020px) {
    .articles {
        &__flex {
            flex-direction: column;
            gap: 20px;
        }
        &__top {
            width: 100%;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            padding-right: 0;
        }
    }
}
@media (max-width: 688px) {
    .articles {
        &__top {
            display: contents;
        }
        &__card {
            padding-top: 14px;
            margin-bottom: 30px;
            &_top {
                flex-direction: column-reverse;
                gap: 20px;
                margin-bottom: 14px;
            }
            &_name {
                font-size: 16px;
            }
            &_date {
                font-size: 12px;
            }
            &_main {
                flex-direction: column;
                gap: 20px;
            }
            &_labels {
                flex-wrap: wrap;
                & p {
                    padding: 6px 10px;
                }
            }
            &_description {
                margin-bottom: 30px;
            }
            &_btn {
                width: 100%;
                text-align: center;
            }
            &_photo img {
                max-height: 220px;
            }
        }
    }
}