// rating-5
.rating {
    padding: 80px 0 160px;
    background: #f9fafb;

    &__top {
        margin-bottom: 50px;
    }

    &__cards {
        display: flex;
        gap: 20px;
    }

    &__card {
        flex: 1 0 30%;
        padding: 30px;
        border-radius: 20px;
        background: var(--belyy);

        &_name {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            margin-bottom: 30px;
        }

        &_estimation {
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 700;
            font-size: 22px;
            color: var(--tekst);
        }

        &_stars {
            display: flex;
            gap: 4px;
        }
        &_star-active {
            & svg path {
                fill: #F69800;
            }
        }

        &_info {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 30px;
            & p:first-child {
                flex: 1 0 45%;
                padding-right: 20px;
                border-right: 1px solid rgba(0, 54, 73, 0.2);
            }
            & span {
                display: block;
                font-weight: 500;
                font-size: 16px;
            }
        }
    }
}


// media rating-5
@media (max-width: 1000px) {
    .rating {
        &__cards {
            flex-direction: column;
            gap: 10px;
        }
    }
}
@media (max-width: 600px) {
    .rating {
        padding: 40px 0;
        &__top {
            margin-bottom: 20px;
        }
        &__card {
            padding: 20px;
            border-radius: 10px;
            &_name {
                margin-bottom: 26px;
            }
            &_logo {
                height: 50px;
            }
            &_star, &_star-active {
                width: 15px;
                height: 15px;
            }
            &_info {
                gap: 20px;
                & span {
                    font-size: 14px;
                }
            }
        }
    }
}