// page-stocks-1
.page-stocks {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 20px;
    }

    &__tab {
        &_btns {
            display: flex;
            gap: 10px;
        }

        &_btn {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--bg-svetlo-seryy);
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: var(--tekst-svetlyy);
            &.tab__btn--active {
                background: var(--color-svetlo-zelenyy-1);
                color: var(--tekst);
            }
        }
    }

    &__quantity {
        font-size: 14px;
        text-align: right;
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    &__card {
        width: calc(100% / 2 - 10px);
        border-radius: 10px;
        padding: 30px;
        background: linear-gradient(12deg, #c7d6dc 0%, #ecf0f1 100%);
        position: relative;
        &:nth-child(4n-2), &:nth-child(4n-1) {
            background: linear-gradient(12deg, #bedbd7 0%, #eaf1f0 100%);
        }

        &_bg {
            font-weight: 700;
            font-size: 207px;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.2);
            position: absolute;
            top: 0;
            right: -100px;
        }

        &_inner {
            position: relative;
            z-index: 2;
            max-width: 68%;
        }

        &_time {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--bg-svetlo-seryy);
            width: fit-content;
            margin-bottom: 20px;
        }

        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: #003649;
            margin-bottom: 10px;
        }

        &_subtitle {
            font-size: 14px;
            line-height: 140%;
            margin-bottom: 30px;
        }
    }
}


// media page-stocks-1
@media (max-width: 900px) {
    .page-stocks {
        &__card {
            width: 100%;
        }
    }
}
@media (max-width: 688px) {
    .page-stocks {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 26px;
        }
        &__quantity {
            text-align: left;
        }
        &__cards {
            gap: 10px;
        }
        &__card {
            border-radius: 10px;
            padding: 20px;
            &:nth-child(n) {
                background: linear-gradient(12deg, #c7d6dc 0%, #ecf0f1 100%);
            }
            &:nth-child(2n) {
                background: linear-gradient(12deg, #bedbd7 0%, #eaf1f0 100%);
            }
            &_bg {
                display: none;
            }
            &_inner {
                max-width: 100%;
            }
            &_title {
                font-size: 18px;
                margin-bottom: 10px;
            }
            &_time {
                font-weight: 400;
                font-size: 12px;
                padding: 6px 12px;
                margin-bottom: 32px;
            }
        }
    }
}
@media (max-width: 550px) {
    .page-stocks {
        &__tab {
            &_btns {
                flex-direction: column;
                gap: 0;
                border-radius: 5px;
                padding: 6px;
                background: var(--bg-svetlo-seryy);
                opacity: 0.7;
            }
        }
    }
}
