// medicament-4
.medicament {
    &__inner {
        display: grid;
        grid-template-columns: 3.4fr 7fr;
        gap: 40px;
    }

    &__sort {
        &_block {
            margin: 0;
        }
        &_search {
            margin-bottom: 10px;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 40px 20px;
        overflow: hidden;
    }

    &__card {
        padding: 20px 30px;
        display: flex;
        flex-direction: column;
        gap: 50px;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            right: -10px;
            display: block;
            width: 1px;
            height: 100%;
            background-color: var(--color-svetlo-zelenyy-2);
        }

        &::before {
            content: "";
            position: absolute;
            bottom: -20px;
            left: 0;
            display: block;
            height: 1px;
            width: calc(100% + 20px);
            background-color: var(--color-svetlo-zelenyy-2);
        }

        &_img {
            max-height: 160px;
            margin: 0 auto;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            margin-bottom: 30px;
        }

        &_name {
            font-weight: 700;
            font-size: 18px;
            line-height: 111%;
            color: var(--tekst);
            margin-bottom: 12px;
        }
    }
}



// media medicament-4
@media (max-width: 1180px) {
    .medicament {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 40px 20px;
        }
    }
}
@media (max-width: 880px) {
    .medicament {
        &__inner {
            grid-template-columns: 1fr;
            gap: 30px;
        }
    }
}
@media (max-width: 660px) {
    .medicament {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 30px;
        }
        &__card {
            padding: 0;
            flex-direction: row;
            gap: 20px;
            padding-bottom: 30px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            &::after, &::before {
                display: none;
            }
            &_img {
                flex: 1 0 35%;
                max-height: none;
                max-width: 100px;
            }
            &_content {
                flex: 1 0 60%;
            }
            &_text {
                margin-bottom: 15px;
            }
            &_name {
                font-size: 16px;
                margin-bottom: 10px;
            }
        }
    }
}