// feedback-1
.feedback {
    &__inner {
        display: flex;
        gap: 20px;
        align-items: stretch;
    }

    &__wrap {
        background: var(--bg-svetlo-seryy);
        padding: 40px;
        border-radius: 15px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        display: flex;
        flex-direction: column;
        flex-basis: 55%;
    }

    &__title {
        font-weight: 700;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 8px;
    }

    &__subtitle {
        font-size: 16px;
        line-height: 150%;
        color: var(--tekst-svetlyy);
        margin-bottom: 15px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 15px
    }

    &__input, &__textarea {
        background-color: var(--fon-plashek);
    }

    &__input:hover, &__textarea:hover {
        background-color: var(--belyy);
        border-color: var(--fon-plashek);
    }

    &__form-input__wrap {
        display: flex;
        gap: 15px;
        font-size: 14px;
        color: var(--tegi);
        &>* {
            flex-basis: calc(50% - 7.5px);
        }
    }

    &__select {
        padding: 16px;
        background-color: var(--fon-plashek);
        color: var(--tekst);
    }

    &__stars {
        display: flex;
        gap: 15px;
        padding-bottom: 10px;
        border-bottom: 1px solid #C7D6DC;
    
        & p {
            color: var(--tegi);
            font-size: 14px;
        }
    }

    &__btn {
        min-width: fit-content;
    }

    &__bottom-wrap {
        display: flex;
        align-items: center;
        gap: 15px;
    }

    &__picture {
        flex-basis: 45%;
        border-radius: 15px;
        overflow: hidden;
        & img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
}


// media feedback-1
@media (max-width: 1024px) {
    .feedback__inner {
        flex-direction: column;
        width: 100%;
    }
    .feedback__wrap {
        flex-basis: 100%;
    }
    .feedback__picture {
        flex-basis: 100%;
        max-height: 400px;
        width: 100%;
    }
    .feedback__title {
        font-size: 30px;
    }
}
@media (max-width: 650px) {
    .feedback__form-input__wrap {
        flex-direction: column;
    }
    .feedback__bottom-wrap {
        flex-direction: column;
    }
    .feedback__btn {
        min-width: 100%;
    }
    .feedback__wrap {
        padding: 40px 20px 20px 20px;
        border-radius: 10px;
    }
    .feedback__subtitle {
        font-size: 14px;
    }
    .feedback__picture {
        border-radius: 10px;
    }
}
@media (max-width: 550px) {
    .feedback__form-input__wrap, .feedback__form, .feedback__bottom-wrap {
        gap: 10px;
    }
    .feedback__inner {
        gap: 10px;
    }
    .feedback__stars {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
    }
}
@media (max-width: 450px) {
    .feedback__picture {
        max-height: 240px;
    }
    .feedback__title {
        font-size: 24px;
    }
}