// contacts-1
.contacts {



    &__row {
        display: flex;
        gap: 50px;
    }


    &__title {
        margin-bottom: 30px;
    }


    &__item {
        font-size: 14px;
        color: var(--tekst-svetlyy);
        display: flex;
        flex-direction: column;
        gap: 8px;

        & a:hover,
        & button:hover {
            text-decoration: underline;
        }
    }

    &__phone {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 28px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--akcentnyy---500);
    }

    &__email {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 28px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--akcentnyy---500);
    }

    &__location {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--akcentnyy---500);

        & button {
            display: block;
            font-weight: 500;
        }
    }

    &__text {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: var(--tekst);
    }

    &__networks {
        display: flex;
        gap: 6px;
    }

    &__network {
        border-radius: 5px;
        padding: 10px;
        width: 40px;
        height: 40px;
        background: var(--color-zelenyy);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.6;
        }
    }



    &__btns {
        display: flex;
        gap: 10px;

        & button {
            flex: 1 0 40%;
        }
    }

    &__maps {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        min-height: 734px;
        position: relative;
        overflow: hidden;
        flex-basis: 60%;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

.general__subtitle {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
    margin-top: 40px;
    max-width: 650px;
    display: block;
}

.general__title-h1--contacts {
    max-width: 1000px;
}

[class*="ymaps-2"][class*="-ground-pane"] {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}

.contacts__item-light {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: rgba(22, 22, 22, 0.4);
    margin-bottom: 4px;
}

.contacts__item-dark {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---temnyy);
}

.contacts__item-dark:not(:last-child) {
    margin-bottom: 16px;
}

.contacts__inner {
    display: flex;
    flex-wrap: wrap;
}

.contacts__item {
    flex: 0 0 calc(23%);
    padding: 0px 20px;
}

.contacts__item:not(:last-child) {
    border-right: 1px solid #eee;
}

.contacts__item:first-child {
    padding-left: 0;
}

.contacts__license p:first-child {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---temnyy);
    margin-bottom: 16px;
}

.contacts__license {
    gap: 0;
}

.contacts__item-mode {
    border-radius: 10px;
    width: 335px;
    height: 198px;
    background: var(--akcentnyy---500);
    display: flex;
    align-items: end;
    padding: 24px;
    position: relative;
}

.contacts__item-mode svg {
    position: absolute;
    top: 24px;
    left: 24px;
}

.contacts__item-mode p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---svetlyy);
}


// media contacts-1
@media (max-width: 900px) {
    .contacts {
        padding: 100px 0;

        &__row {
            flex-direction: column-reverse;
            gap: 40px;
        }

        &__maps {
            min-height: 500px;
        }
    }

    .general__subtitle-contacts {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--tekst---seryy);
        margin-top: 20px;
    }
}

@media (max-width: 700px) {
    .contacts {
        padding: 80px 0;


        &__title {
            margin-bottom: 20px;
        }

        &__inner {
            margin-bottom: 20px;
        }

        &__btns {
            flex-direction: column;
        }

        &__maps {
            min-height: 447px;
        }
    }
}

@media (max-width: 1400px) {
    .contacts__item-mode {
        width: 100%;
        height: 198px;
    }
}

@media (max-width: 1400px) {
    .contacts__item {
        flex: 0 0 50%;
        padding: 20px;
        border-right: 1px solid #eee;
        border-bottom: 1px solid #eee;
    }

    .contacts__item:last-child {
        border-right: 0px solid #eee;
        border-bottom: 0px solid #eee;
    }

    .contacts__item:nth-child(2) {
        border-right: 0px solid #eee;
    }

    .contacts__license {
        padding-left: 0;
        border-bottom: 0px solid #eee;
    }

}

@media (max-width: 800px) {
    .general__head--contacts {
        padding-bottom: 20px;
    }

    .general__section-contacts {
        padding-top: 0;
    }

    .contacts__item {
        flex: 0 0 100%;
        padding: 20px;
        border-right: 0px solid #eee;
        border-bottom: 1px solid #eee;
    }

    .contacts__item:first-child {
        border-top: 1px solid #eee;
    }

    .contacts__item-mode {
        padding: 16px 16px 16px 24px;
    }

    .contacts__item:last-child {
        border-right: 0px solid #eee;
        border-bottom: 0px solid #eee;
        padding: 0;
    }

    .contacts__item:not(:last-child) {
        border-right: 0px solid #eee;
    }

    .contacts__item-mode {
        width: 100%;
        height: auto;
    }

    .contacts__item:nth-child(2) {
        border-right: 0px solid #eee;
        padding-left: 0;
    }

    .contacts__item-mode svg {
        top: 16px;
        left: 16px;
    }

    .contacts__item-mode p {
        padding-left: 41px;
    }

    .contacts__license {
        padding-left: 0;
        border-bottom: 1px solid #eee;
    }

    .contacts__phone {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--akcentnyy---500);
    }

    .contacts__item-light {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: rgba(22, 22, 22, 0.4);
    }

    .contacts__location {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--akcentnyy---500);
    }
}