// stages-7
.stages {
    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        & ol {
            list-style: none;
            counter-reset: item;
            display: flex;
            flex-direction: column;
            & li {
                width: calc(50% - 20px);
                display: flex;
                gap: 10px;
                position: relative;
                z-index: 4;
                &:not(:first-child){
                    margin-top: -10px;
                }
                &:nth-child(2n) {
                    margin-left: auto;
                }
                &::before {
                    display: block;
                }
                &:nth-child(1), &:nth-child(2), &:nth-child(4), &:nth-child(5), &:nth-child(6) {
                    &::before {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        z-index: 1;
                    }
                }
                &:nth-child(odd) {
                    &::before {
                        left: 100%;
                    }
                }
                &:nth-child(even) {
                    &::before {
                        left: -100%;
                    }
                }
                &:nth-child(1) {
                    &::before {
                        height: 110px;
                        width: 343px;
                        background: url("../img/stages/stages-7/icon1.svg") center center no-repeat;
                    }
                }
                &:nth-child(2) {
                    &::before {
                        height: 64px;
                        width: 214px;
                        right: 100%;
                        left: auto;
                        background: url("../img/stages/stages-7/icon2.svg") center center no-repeat;
                    }
                }
                &:nth-child(4) {
                    &::before {
                        left: -169px;
                        height: 86px;
                        bottom: auto;
                        top: 5px;
                        width: 169px;
                        background: url("../img/stages/stages-7/icon3.svg") center center no-repeat;
                    }
                }
                &:nth-child(5) {
                    &::before {
                        height: 94px;
                        bottom: auto;
                        top: 10px;
                        width: 300px;
                        background: url("../img/stages/stages-7/icon4.svg") center center no-repeat;
                    }
                }
                &:nth-child(6) {
                    &::before {
                        height: 98px;
                        bottom: auto;
                        top: 10px;
                        left: -230px;
                        width: 230px;
                        background: url("../img/stages/stages-7/icon5.svg") center center no-repeat;
                    }
                }
            }
        }
    }

    &__inner {
        border-radius: 20px;
        padding: 34px;
        box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.08);
        background: var(--belyy);
        position: relative;
    }

    &__name {
        font-weight: 700;
        font-size: 22px;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 10px;
        &::before {
            content: counter(item); 
            counter-increment: item;
            border-radius: 5px;
            width: 30px;
            height: 30px;
            background: var(--color-svetlo-zelenyy-2);
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--color-zelenyy);
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 15px;
            right: 15px;
        }
    }

    &__text {
        font-size: 14px;
    }

    &__img {
        & img {
            display: block;
            border-radius: 20px;
            min-width: 192px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}


// media stages-7
@media (max-width: 1150px) {
    .stages {
        &__cards {
            & ol {
                display: flex;
                flex-direction: column;
                gap: 20px;
                & li {
                    width: 100%;
                    &:not(:first-child){
                        margin-top: 0;
                    }
                    &:nth-child(2n) {
                        margin-left: 0;
                    }
                    &::before {
                        display: none;
                    }
                }
            }
        }
    }
}
@media (max-width: 620px) {
    .stages {
        &__title {
            margin-bottom: 20px;
        }
        &__inner {
            border-radius: 10px;
            padding: 18px;
        }
        &__name {
            font-size: 16px;
            width: calc(100% - 40px);
            &::before {
                width: 25px;
                height: 25px;
                font-size: 14px;
            }
        }
        &__text {
            font-size: 14px;
        }
        &__img {
            display: none;
        }
    }
}