// page-articles-1
.page-articles {
    &__search {
        position: relative;
        width: 100%;
        margin-bottom: 30px;
        & input {
            background: rgba(234, 237, 242, 0.8);
            opacity: 0.7;
            width: 100%;
            &:focus {
                opacity: 1;
            }
        }
        & img {
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translate(0, -50%);
        }
    }


    &__grid {
        display: grid;
        grid-template-columns: 3.4fr 6.8fr;
        gap: 60px;
    }

    &__sort {
        &_text {
            color: var(--tekst);
            margin-bottom: 20px;
        }

        &_item {
            margin-bottom: 20px;
            display: flex;
            gap: 14px;
            align-items: center;

            & input[type="checkbox"] {
                position: relative;
                width: 28px;
                height: 28px;
                color: #000;
                border: 1px solid #788189;
                border-radius: 5px;
                appearance: none;
                outline: 0;
                cursor: pointer;
                transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
                &::before {
                    position: absolute;
                    content: '';
                    display: block;
                    top: 3px;
                    left: 9px;
                    width: 8px;
                    height: 14px;
                    border-style: solid;
                    border-color: #fff;
                    border-width: 0 3px 3px 0;
                    transform: rotate(45deg);
                    opacity: 0;
                }
                &:checked {
                    color: #fff;
                    border-color: var(--color-zelenyy);
                    background: var(--color-zelenyy);
                    &::before {
                        opacity: 1;
                    }
                }
            }
            & label {
                position: relative;
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst);
                &::before {
                    position: absolute;
                    content: attr(data-content);
                }
            }
        }

        &_quantity {
            font-size: 14px;
            color: var(--tekst-svetlyy);
        }
    }

    &__card {
        display: flex;
        gap: 30px;
        padding-top: 20px;
        border-top: 1px solid #c7d6dc;

        &:not(:last-child){
            margin-bottom: 40px;
        }

        &_info {
            flex: 1 0 60%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }

        &_img {
            flex: 1 0 30%;
            display: block;
            min-height: 100%;
            max-width: 33%;
            width: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
            }
        }

        &_title {
            font-weight: 700;
            font-size: 18px;
            color: var(--tekst);
            margin-bottom: 10px;
        }

        &_labels {
            display: flex;
            gap: 4px;
            flex-wrap: wrap;
        }

        &_label {
            border-radius: 8px;
            padding: 6px 12px;
            background: var(--bg-svetlo-seryy);
            font-size: 12px;
            color: var(--color-temno-zelenyy);
        }

        &_description {
            color: var(--tekst);
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            font-size: 14px;
            line-height: 140%;
            color: rgba(64, 95, 106, 0.7);
            text-align: right;
            & span {
                text-wrap: nowrap;
            }
        }

        &_btn {
            border-radius: 10px;
            padding: 10px 20px;
            background: #e3edee;
        }
    }
}


// media page-articles-1
@media (max-width: 980px) {
    .page-articles {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 30px;
        }
    }
}

@media (max-width: 688px) {
    .page-articles {
        &__sort {
            &_text {
                margin-bottom: 14px;
            }
            &_item {
                margin-bottom: 14px;
                & input[type="checkbox"] {
                    width: 22px;
                    height: 22px;
                    &::before {
                        top: 3px;
                        left: 7px;
                        width: 6px;
                        height: 10px;
                        border-width: 0 2px 2px 0;
                    }
                }
                & label {
                    font-size: 16px;
                }
            }
            &_quantity {
                margin-top: 30px;
                font-size: 12px;
            }
        }

        &__card {
            flex-direction: column;
            gap: 20px;
            padding-top: 16px;
            &:not(:last-child){
                margin-bottom: 30px;
            }
            &_img {
                max-width: 100%;
                max-height: 210px;
                & img {
                    max-height: 210px;
                }
            }
            &_title {
                font-size: 16px;
                margin-bottom: 14px;
            }
            &_bottom {
                flex-direction: column-reverse;
                align-items: stretch;
                gap: 14px;
                font-size: 12px;
                text-align: left;
            }
            &_btn {
                text-align: center;
            }
        }
    }
}
