// calculator-3
.calculator {
    &__title {
        margin-bottom: 50px;
    }

    &__row {
        display: flex;
        gap: 40px;
    }

    &__left {
        flex: 1 0 43%;
        padding-right: 40px;
        border-right: 1px solid #c7d6dc;
    }

    &__selects {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__btn {
        display: none;
    }

    &__right {
        flex: 1 0 40%;
    }

    &__result {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 20px;
        &_item {
            color: var(--tekst);
            & span {
                display: block;
                font-size: 14px;
                color: var(--tekst-svetlyy);
                margin-bottom: 10px;
            }
        }
    }

    &__price {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        padding-bottom: 4px;
        border-bottom: 1px solid #c7d6dc;
        margin-bottom: 20px;
        & span {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
        }
    }

    &__form {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        &_input {
            flex: 1 0 40%;
            & input {
                width: 100%;
                background: var(--bg-svetlo-seryy);
            }
        }
        &_btn, &_politic {
            flex: 1 0 100%;
        }
    }

    &__slider {
        display: flex;
        flex-direction: column;
        gap: 8px;
        &_output {
            display: block;
            border-radius: 5px;
            padding: 12px 20px;
            padding: 30px 20px 8px;
            background: var(--fon-plashek);
            opacity: 0.7;
            font-weight: 500;
            color: var(--tekst);
        }
        &_container {
            width: 100%; 
        }
        &_values {
            display: flex;
            justify-content: space-between;
            gap: 10px;
            color: var(--tekst);
        }
    }
}



// media calculator-3
@media (max-width: 1000px) {
    .calculator {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
        &__left {
            padding-right: 0;
            border-right: none;
            padding-bottom: 30px;
            border-bottom: 1px solid #c7d6dc;
        }
        &__selects {
            margin-bottom: 30px;
        }
        &__btn {
            display: block;
        }
    }
}
@media (max-width: 688px) {
    .calculator {
        &__title {
            margin-bottom: 20px;
        }
        &__selects {
            gap: 12px;
        }
        &__btn {
            width: 100%;
        }
        &__result {
            gap: 14px;
            margin-bottom: 30px;
            &_item {
                & span {
                    font-size: 12px;
                    margin-bottom: 8px;
                }
            }
        }
        &__price {
            font-size: 12px;
            margin-bottom: 20px;
            & span {
                font-size: 18px;
            }
        }
        &__form {
            flex-direction: column;
        }
    }
}

