
// service-submenu-2
.service-submenu-2 {
    position: fixed;
    top: 245px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: calc(var(--container-width) - 30px);
    background-color: transparent;
    padding: 0;

    &__row {
        display: flex;
        gap: 10px;
        width: 100%; 
    }

    & .header {
        &__submenu {
            &_tabs {
                flex: 1 0 70%;
                margin: 0 auto;
                border-radius: 20px;
                padding: 30px;
                background: var(--belyy);
            }
    
            &_tab-btns {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-wrap: wrap;
                    flex-wrap: wrap;
                gap: 3px;
                border-radius: 10px;
                padding: 5px;
                background: var(--color-svetlo-zelenyy-2);
                width: fit-content;
                margin-bottom: 30px;
            }
    
            &_tab-btn {
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                color: var(--tekst);
                border-radius: 10px;
                padding: 14px 21px;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
    
                &:not(.header__submenu_tab-btn.active):hover {
                    background: var(--color-svetlo-zelenyy-1);
                }
                &.active {
                    color: var(--color-temno-zelenyy);
                    background: var(--belyy);
                }
            }
    
            &_tab-contents {
                flex: 1 0 50%;
            }
    
            &_tab-content {
                display: none;
                width: 100%;
                -webkit-box-ordinal-group: 11;
                    -ms-flex-order: 10;
                        order: 10;
                &.active {
                    display: block;
                }
    
                & ul {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    align-items: stretch;
                    gap: 10px;
                    max-height: 470px;
                    overflow-y: auto;
                    padding-right: 10px;
                    &::-webkit-scrollbar {
                        width: 4px;
                        background-color: var(--izobrazhenie);
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background-color: var(--color-temno-zelenyy);
                    }
                    &::-webkit-scrollbar-track {
                        background-color: var(--izobrazhenie);
                    }
    
                    & li {
                        min-width: auto;
                        max-width: 100%;
                        min-height: auto;
                    }
    
                    & a {
                        border: 1px solid var(--color-svetlo-zelenyy-1);
                        border-radius: 10px;
                        padding: 18px 18px;
                        font-weight: 500;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 140%;
                        color: var(--tekst);
                        display: flex;
                        flex-direction: column;
                        gap: 6px;
                        height: 100%;
                        -webkit-transition: all 0.3s ease;
                        transition: all 0.3s ease;
                        &:hover {
                            background: var(--color-svetlo-zelenyy-2);
                            opacity: 1;
                        }
                    }
                    & span {
                        display: block;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 150%;
                        color: var(--tekst-svetlyy);
                    }
                }
            }
    
            &_stocks {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                    -ms-flex-direction: column;
                        flex-direction: column;
                gap: 10px;
                flex: 1 0 25%;
            }
        }
    
        &__stock {
            position: relative;
            border-radius: 20px;
            padding: 30px;
            background: var(--color-zelenyy);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 50px;
            flex: 1 0 30%;

            &::after {
                content: url("../img/icons/header-submenu-discount.svg");
                position: absolute;
                bottom: 25px;
                left: 30px;
            }
    
            &_date {
                font-weight: 400;
                font-size: 12px;
                line-height: 140%;
                color: var(--color-svetlo-zelenyy-1);
                padding-bottom: 12px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                margin-bottom: 12px;
            }
    
            &_title {
                font-weight: 700;
                font-size: 18px;
                line-height: 140%;
                color: var(--belyy);
                margin-bottom: 10px;
            }
    
            &_subtitle {
                font-weight: 400;
                font-size: 14px;
                line-height: 140%;
                color: rgba(255, 255, 255, 0.8);
            }
    
            &_link {
                display: flex;
                justify-content: end;
                & a {
                    background: var(--belyy);
                }
            }
        }
    }
}


@media (max-width: 1260px) {
    .service-submenu-2 {
        position: static;
        width: 100%;
        margin: 20px 0;
        transform: translateX(0);
    
        &__row {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            gap: 20px;
            width: 100%;
            padding: 0;
        }
    
        & .header {
            &__submenu {
                &_tabs {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    border-radius: 10px;
                    background: var(--color-svetlo-zelenyy-2);
                    width: 100%;
                }
                &_tab-btns {
                    display: contents;
                }
                &_tab-btn {
                    border: 1px solid var(--color-svetlo-zelenyy-1);
                    border-radius: 5px;
                    padding: 12px 36px;
                    width: 100%;
                    &.active {
                        border: 1px solid var(--belyy);
                    }
                }
        
                &_tab-contents {
                    display: contents;
                }
        
                &_tab-content {
                    width: 100%;
                    border-radius: 10px;
                    padding: 10px 0 20px;
                    gap: 0;
                    & ul {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        -webkit-box-pack: justify;
                            -ms-flex-pack: justify;
                                justify-content: space-between;
                        gap: 10px 20px;
                        max-height: 360px;
                        padding: 0;
                        padding-right: 8px;
                    }
                }
                &_stocks {
                    flex-basis: 100%;
                    margin-top: 0;
                    order: 100;
                    flex-direction: row;
                    overflow-x: auto;
                    scroll-snap-type: x mandatory;
                    padding-bottom: 10px;
                    &::-webkit-scrollbar {
                        height: 4px;
                        background-color: var(--izobrazhenie);
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background-color: var(--color-temno-zelenyy);
                    }
                    &::-webkit-scrollbar-track {
                        background-color: var(--izobrazhenie);
                    }
                }
            }
        
            &__stock {
                position: relative;
                border-radius: 10px;
                padding: 20px;
                gap: 30px;
                width: 80%;
                scroll-snap-align: start;
                &::after {
                    bottom: 0;
                    left: 20px;
                    transform: scale(0.6);
                }
                &_date {
                    font-size: 12px;
                }
                &_title {
                    font-size: 16px;
                    margin-bottom: 10px;
                }
                &_subtitle {
                    font-size: 14px;
                }
            }
        }
    }
}
@media (max-width: 800px) {
    .service-submenu-2 .header {
        &__submenu {
            &_tabs {
                padding: 8px;
            }
            &_tab-content {
                & ul {
                    & a {
                        padding: 14px 16px;
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
@media (max-width: 650px) {
    .service-submenu-2 .header {
        &__submenu {
            &_tab-content {
                & ul {
                    grid-template-columns: repeat(1, 1fr);
                    gap: 10px;
                }
            }
        }
        &__stock {
            min-width: 90%;
        }
    }
}


