// filials-3
.filials {
    &__title {
        margin-bottom: 50px;
    }

    &__inner {
        position: relative;
    }

    &__sort {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        position: absolute;
        top: 10px;
        left: 10px;
        right: 10px;
        z-index: 3;

        &_selects {
            width: 40%;
        }
        
        &_block {
            & select {
                box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
                background: var(--belyy) url(./../img/icons/arrow-bottom.svg) no-repeat right;
                background-position-x: calc(100% - 8px);            
                opacity: 1;
            }
        }
    }

    &__quantity {
        border-radius: 5px;
        padding: 22px 30px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
        background: var(--belyy);
        font-size: 14px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
    }

    &__item {
        border-radius: 5px;
        padding: 30px 40px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
        background: var(--belyy);
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        z-index: 3;

        &_row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
        }

        &_name {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 10px;
        }

        &_right {
            display: flex;
            align-items: center;
            gap: 24px;
        }

        &_info {
            &:not(:last-child) {
                padding-right: 24px;
                border-right: 1px solid var(--color-svetlo-zelenyy-2);
            }
            & p:first-child {
                font-size: 14px;
                color: var(--tekst-svetlyy);
                margin-bottom: 8px;
            }
            & a, & p:last-child {
                font-weight: 700;
                font-size: 16px;
                line-height: 140%;
                color: var(--color-temno-zelenyy);
            }
        }
    }

    &__maps {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        min-height: 700px;
        position: relative;
        overflow: hidden;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}


// media filials-3
@media (max-width: 1100px) {
    .filials {
        &__sort {
            &_selects {
                width: 60%;
            }
        }
        &__item {
            &_row {
                flex-direction: column;
                align-items: stretch;
                gap: 22px;
            }
        }
    }
}
@media (max-width: 720px) {
    .filials {
        &__title {
            margin-bottom: 20px;
        }
        &__sort {
            flex-direction: column;
            align-items: stretch;
            gap: 6px;
            &_selects {
                width: 100%;
            }
        }
        &__quantity {
            padding: 11px 30px;
            text-align: center;
        }
        &__item {
            padding: 22px 20px;
            &_name {
                font-size: 18px;
            }
            &_right {
                flex-direction: column;
                align-items: stretch;
                gap: 14px;
            }
            &_info {
                &:not(:last-child) {
                    padding-right: 0;
                    border-right: none;
                    padding-bottom: 14px;
                    border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                }
            }
        }
        &__maps {
            border-radius: 10px;
            min-height: 823px;
        }
    }
}