// article-4
.article {
    &__grid {
        display: grid;
        grid-template-columns: 8fr 4fr;
        gap: 50px;
    }

    &__block {
        &:not(:last-child) {
            margin-bottom: 60px;
        }
    }

    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
        font-weight: 700;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 20px;
    }

    & h2 {
        font-size: 34px;
    }

    & h3 {
        font-size: 30px;
    }

    & h4 {
        font-size: 26px;
    }

    & h5 {
        font-size: 18px;
    }

    & h2:empty,
    & h3:empty,
    & h4:empty,
    & h5:empty,
    & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    & span {
        font-weight: 500;
    }

    & a:not(.phone-btn-white) {
        font-weight: 500;
        text-decoration: underline;
        color: var(--tekst-svetlyy);
        transition: all 0.3s ease;

        &:hover {
            color: var(--color-zelenyy);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;

        & li {
            display: flex;
            gap: 10px;

            &::before {
                content: "";
                display: block;
                min-width: 20px;
                width: 20px;
                height: 20px;
                border-radius: 100%;
                background-color: var(--color-temno-zelenyy);
                margin-top: 2px;
                border: 5px solid #fff;
                outline: 2px solid rgba(32, 97, 117, 0.1);
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;

        & li {
            display: flex;
            gap: 10px;

            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 28px;
                width: 28px;
                height: 28px;
                background: var(--color-svetlo-zelenyy-2);
                border-radius: 20px;
                font-weight: 500;
                font-size: 12px;
                color: var(--tekst-svetlyy);
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__img,
    &__img-fill {
        margin-bottom: 20px;
        border-radius: 20px;
        overflow: hidden;
        max-height: 450px;
        width: 100%;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 20px;
            margin-bottom: 10px;
        }

        & p {
            font-size: 14px;
            color: rgba(64, 95, 106, 0.8);
            margin: 0;
        }
    }

    &__img {
        float: right;
        margin-left: 20px;
        max-width: 40%;
    }

    &__form {
        position: relative;
        border-radius: 20px;
        padding: 30px;
        background: linear-gradient(210deg, #6aa5ae 0%, #3d7c85 100%);
        color: var(--belyy);
        overflow: hidden;

        position: sticky;
        top: 200px;

        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 120%;
            margin-bottom: 30px;
        }

        &_bg {
            position: absolute;
            top: 30px;
            right: -78px;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: none;
        border-collapse: collapse;
        border: 1px solid var(--color-svetlo-zelenyy-2);
        border-radius: 10px;
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                background: var(--color-svetlo-zelenyy-2);
                font-weight: 500;
                font-size: 14px;
                color: var(--tekst);
                border-right: 1px solid 1px solid rgba(210, 220, 232, 0.5);
            }

            & tr th:first-child {
                border-radius: 10px 0 0 0;
            }

            & tr th:last-child {
                border-right: none;
                border-radius: 0 10px 0 0;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid #f2f8f7;
                border-bottom: 1px solid #f2f8f7;
                padding: 20px;
                font-size: 14px;
                color: var(--tekst-svetlyy);
                vertical-align: top;
            }

            & tr td:last-child {
                border-right: none;
            }

            & tr:last-child td {
                border-bottom: none;
            }
        }
    }

    &__video {
        position: relative;
        width: 100%;
        border-radius: 10px;
        overflow: hidden;

        & video {
            width: 100%;
            display: block;
        }

        &_play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            z-index: 10;
            transition: all 0.3s ease;

            &:hover {
                transform: translate(-50%, -50%) scale(1.1);
            }
        }
    }


    &__literature {
        margin-top: 50px;

        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 30px;
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            border-top: 1px solid var(--color-svetlo-zelenyy-2);
            padding: 20px 0px;
        }

        &_item {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            gap: 15px;

            & p {
                margin: 0 !important;
            }
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background-color: var(--color-zelenyy);
        }
    }
}



// media article-2
@media (max-width: 1100px) {
    .article {
        &__grid {
            display: flex;
            flex-direction: column;
        }

        &__right {
            display: none;
        }

        & h2,
        & h3,
        & h4,
        & h5,
        & h6 {
            order: -2;
        }

        &__img {
            order: -1;
            margin-bottom: 20px;

            & img {
                max-height: 400px;
            }
        }

        &__form {
            order: -3;
            margin-bottom: 40px;
        }
    }
}

@media (max-width: 688px) {
    .article {
        &__block {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }

        & h2 {
            font-size: 24px;
        }

        & h3 {
            font-size: 22px;
        }

        & h4 {
            font-size: 20px;
        }

        & h5 {
            font-size: 16px;
        }

        & ul li {
            &::before {
                min-width: 8px;
                width: 8px;
                height: 8px;
            }
        }

        &__img {
            & img {
                max-height: 230px;
            }

            & p {
                font-size: 12px;
            }
        }

        &__img,
        &__img-fill {
            float: none;
            max-width: 100%;
            margin: 0 0 20px 0;
            border-radius: 10px;

            & img {
                border-radius: 10px;
                min-height: 220px;
                max-height: 300px;
            }

            & p {
                font-size: 12px;
            }
        }

        & table {
            overflow-x: auto;
            max-width: 100%;

            & thead th,
            & tbody td {
                min-width: 243px;
            }

            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }

        &__literature {
            margin-top: 30px;

            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }

            & ol {
                margin-bottom: 17px;

                & li {
                    font-size: 12px;

                    &::before {
                        font-size: 14px;
                    }
                }
            }

            &_bottom {
                flex-direction: column;
                align-items: stretch;
                gap: 14px;
                padding: 14px 0;
            }

            &_item {
                font-size: 14px;
                align-items: center;
                justify-content: space-between;

                &:first-child {
                    padding-bottom: 14px;
                    border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                }
            }
        }
    }
}

