.popup-review {
    &__body {
        display: flex;
        justify-content: flex-end;
        align-items: stretch;
    }

    &__content {
        max-width: 640px;
        border-radius: 10px;
        padding: 60px 80px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--bg-svetlo-seryy);

        .close-popup:hover {
            transform: scale(0.94);
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        height: 100%;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__title {
        font-weight: 700;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 8px;
    }

    &__subtitle {
        line-height: 150%;
        color: var(--tekst-svetlyy);
    }

    &__inputs {
        display: flex;
        flex-direction: column;
        gap: 18px;
    }

    &__input:hover, &__textarea:hover {
        background-color: var(--belyy);
    }

    &__select {
        background-color: var(--belyy);
        opacity: 1;
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__close {
        width: 60px;
        height: 60px;
        top: 0;
        left: -70px;
    }
}

.popup-review-response {
    &__content {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 30px;
    }
    &__text {
        text-align: center;
        & h3 {
            text-align: center;
            font-weight: 700;
            font-size: 34px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            flex-direction: column;
            gap: 30px;
            margin-bottom: 12px;
            &::before {
                content: "";
                display: block;
                width: 140px;
                aspect-ratio: 1/1;
                margin: 0 auto;
            }
        }
    }
}

.popup[data-target="popup-review-ok"] {
    & h3::before {
        background-image: url("./../img/popups/popup-call-2-ok.svg");
        background-size: 100% 100%;
    }
}
.popup[data-target="popup-review-error"] {
    & h3::before {
        background-image: url("./../img/popups/popup-call-2-error.svg");
        background-size: 100% 100%;
    }
    & .popup-review-response__btn {
        display: none;
    }
}


// media popup-review
@media (max-width: 740px) {
    .popup-review {
        &__body {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &__content {
            border-radius: 10px;
            padding: 20px;
        }
        &__text {
            margin-bottom: 20px;
        }
        &__title {
            font-size: 18px;
            margin-bottom: 8px;
        }
        &__inputs {
            gap: 12px;
        }
        &__field {
            flex: 1 0 100%;
        }
        &__estimation {
            flex-direction: column;
            gap: 10px;
            align-items: start;
        }
        &__bottom {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
        &__close {
            width: 30px;
            height: 30px;
            top: -40px;
            left: auto;
            right: 0;
        }
    }
    .popup-review-response {
        &__content {
            padding: 80px 20px 100px 20px;
            max-width: 400px;
        }
        &__text {
            & h3 {
                font-size: 20px;
                margin-bottom: 8px;
                &::before {
                    width: 60px;
                }
            }
        }
    }
}