// tariff-1
.tariff {
    overflow: hidden;

    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        border-radius: 20px;
        padding: 40px;
        background: var(--bg-svetlo-seryy);
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 30px;
        
        &_row {
            display: flex;
            gap: 60px;
        }

        &_right {
            flex: 1 0 50%;
        }

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
            padding-bottom: 8px;
            border-bottom: 1px solid #c7d6dc;
            margin-bottom: 40px;
        }

        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
        }

        &_price {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--color-zelenyy);
        }

        &_list {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & li {
                display: flex;
                gap: 10px;
                &::before {
                    content: "";
                    display: block;
                    min-width: 10px;
                    width: 10px;
                    height: 10px;
                    border-radius: 100%;
                    margin-top: 7px;
                }
            }
        }

        &_item {
            &-includes {
                color: var(--tekst);
                &::before {
                    background: var(--color-zelenyy);
                }
            }
            &-no-includes {
                color: var(--tekst-svetlyy);
                &::before {
                    background: rgba(32, 97, 117, 0.4);
                }
            }
        }

        &_btn {
            width: 100%;
        }
    }

    &__swiper-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    &__swiper-pagination {
        &.swiper-pagination-fraction {
            font-weight: 700;
            font-size: 20px;
            color: rgba(61, 124, 133, 0.6);
        
            .swiper-pagination-current {
                font-weight: 700;
                font-size: 26px;
                color: var(--color-zelenyy);
            }
        }
    }

    &-card {
        &__swiper {
            height: auto;
        }
    
        &__swiper-slide {
            height: auto;
            &_image {
                & img {
                    width: 100%;
                    height: 100%;
                    min-height: 200px;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }
    
        &__swiper-pagination {
            position: absolute;
            bottom: 10px;
            left: 20px;
            right: 20px;
            & .swiper-pagination-bullet {
                background-color: #fff;
            }
        }
    }
}

// media tariff-1
@media (max-width: 900px) {
    .tariff {
        &__card {
            gap: 30px;
            height: auto;
            &_row {
                flex-direction: column-reverse;
                gap: 30px;
            }
        }
    }
}
@media (max-width: 688px) {
    .tariff {
        &__title {
            margin-bottom: 30px;
        }
        &__cards {
            gap: 20px;
            border-radius: 10px;
            padding: 20px;
        }
        &__card {
            gap: 20px;
            &_row {
                gap: 20px;
            }
            &_top {
                margin-bottom: 20px;
            }
            &_title {
                font-size: 16px;
            }
            &_price {
                font-size: 14px;
            }
            &_list {
                & li {
                    &::before {
                        min-width: 6px;
                        width: 6px;
                        height: 6px;
                    }
                }
            }
        }
        &__swiper-pagination {
            &.swiper-pagination-fraction {
                font-size: 16px;
                .swiper-pagination-current {
                    font-size: 24px;
                }
            }
        }
    }
}