// legal-info-4
.legal-info {
    &__inner {
        display: flex;
        gap: 40px;
    }

    &__left {
        flex: 1 0 62%;
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
    }

    &__right {
        flex: 1 0  30%;
    }

    &__block {
        margin-bottom: 60px;
    }

    &__accordion{
        display: contents;
    }

    &__btn-more {
        display: none;
    }

    &__supervisions {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__supervision {
        & p {
            &:not(:last-child) {
                margin-bottom: 8px;
            }
            &:first-child {
                font-weight: 700;
                font-size: 16px;
                line-height: 140%;
                color: var(--color-zelenyy);
                padding-bottom: 16px;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-1);
                margin-bottom: 16px;
            }
        }
    }

    &__green-block {
        border-radius: 10px;
        padding: 30px;
        background: var(--color-svetlo-zelenyy-2);
    }
}


// media legal-info-4
@media (max-width: 1000px) {
    .legal-info {
        &__inner, &__left, &__right {
            display: contents;
        }
    }
}
@media (max-width: 700px) {
    .legal-info {
        &__block {
            flex-direction: column;
            gap: 0;
            margin-bottom: 20px;
        }
        &__accordion {
            display: block;
            &.accordion--active {
                & .legal-info__btn-more svg path:last-child{
                    transform: rotateX(90deg);
                }
            }
        }
        &__title {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }
        &__btn-more {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 20px;
            border: 1px solid var(--tekst-svetlyy);
            min-width: 28px;
            width: 28px;
            height: 28px;
            & svg path {
                transition: all 0.3s ease;
            }
        }
        &__text {
            &.accordion__content--active {
                margin-bottom: 20px;
            }
        }
        &__supervisions {
            flex-direction: column;
        }
        &__supervision {
            & p:first-child {
                font-size: 14px;
            }
        }
        &__green-block {
            padding: 20px;
        }
    }
}
