// intro-stock-1
.intro-stock {
    &__breadcrumbs {
        margin-bottom: 20px;
    }

    &__row {
        display: flex;
        gap: 20px;
    }

    &__main {
        flex: 1 0 60%;
        border-radius: 20px;
        padding: 40px 65px;
        background: rgba(234, 237, 242, 0.5);
        & h1 {
            margin-bottom: 15px;
        }

        &_text {
            margin-bottom: 40px;
        }

        &_time {
            border-radius: 5px;
            padding: 10px 14px;
            background: #dce6e9;
            display: flex;
            align-items: center;
            gap: 12px;
            width: fit-content;
            & span {
                font-weight: 500;
                color: var(--color-temno-zelenyy);
            }
            & img {
                min-width: 14px;
            }
        }
    }


    &__form {
        flex: 1 0 30%;
        border-radius: 15px;
        padding: 30px;
        background: rgba(234, 237, 242, 0.5);

        &_text {
            margin-bottom: 30px;
        }

        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 15px;
        }

        &_subtitle {
            font-size: 14px;
        }

        &_inner {
            display: flex;
            flex-direction: column;
            gap: 12px;
        }
    }
}

// media intro-stock-1
@media (max-width: 1000px) {
    .intro-stock {
        &__row {
            flex-direction: column;
            gap: 12px;
        }
    }
}
@media (max-width: 688px) {
    .intro-stock {
        &__main {
            border-radius: 10px;
            padding: 20px;
            &_text {
                margin-bottom: 20px;
            }
            &_time {
                padding: 10px;
                align-items: flex-start;
            }
        }
        &__form {
            border-radius: 15px;
            padding: 20px;
            &_text {
                margin-bottom: 20px;
            }
            &_title {
                font-size: 18px;
                margin-bottom: 12px;
            }
        }
    }
}