// page-page-faq-4
.page-faq {
    &__grid {
        display: grid;
        grid-template-columns: 6.2fr 4fr;
        gap: 30px;
    }

    &__tab {
        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 20px;
        }
        &_btns {
            border: 1px solid var(--color-svetlo-zelenyy-2);
            border-radius: 7px;
            width: fit-content;
            display: flex;
            flex-wrap: wrap;
        }
        &_btn {
            border-radius: 5px;
            padding: 16px 20px;
            font-weight: 500;
            font-size: 14px;
            &.active {
                border: 1px solid var(--color-svetlo-zelenyy-1);
                background: var(--color-svetlo-zelenyy-2);
                color: var(--tekst);
                opacity: 0.8;
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 14px;
        list-style-type: none;
        counter-reset: item;
    }

    &__button {
        border-radius: 10px;
        padding: 32px;
        background: var(--bg-svetlo-seryy);
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        gap: 0 20px;
        text-align: left;
        line-height: 140%;
        border: 1px solid transparent;
        &::before {
            content: "Вопрос №" counter(item);
            counter-increment: item;
            display: block;
            font-size: 14px;
            line-height: 140%;
            color: #ff9500;
            flex-basis: 70%;
        }
    }
    &__button.accordion--active {
        gap: 20px;
        border: 1px solid var(--color-svetlo-zelenyy-1);
        & .page-faq__btn-more {
            & svg path:last-child {
                transform: rotateX(90deg);
            }
        }
    }

    &__name {
        font-weight: 700;
        font-size: 22px;
        line-height: 140%;
        color: var(--color-temno-zelenyy);
        flex-basis: 100%;
    }
    
    &__content {
        flex-basis: 100%;
        overflow: hidden;
        transition: all 0.3s ease;
    }

    &__btn-more {
        display: block;
        width: 34px;
        height: 34px;
        border: 1px solid var(--tekst-svetlyy);
        border-radius: 20px;
        & svg path {
            transition: all 0.3s ease;
        }
    }

    &__editor {
        border-radius: 10px;
        padding: 20px 30px;
        background: var(--belyy);
        display: flex;
        align-items: center;
        gap: 30px;
        margin-top: 20px;

        &_left {
            display: flex;
            gap: 16px;
            align-items: center;
            padding-right: 30px;
            border-right: 1px solid var(--color-svetlo-zelenyy-2);
            flex-basis: 50%;
            flex: 1 0 50%;
        }

        &_img {
            display: block;
            border-radius: 60px;
            min-width: 90px;
            width: 90px;
            height: 90px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            font-size: 14px;
            line-height: 140%;
            color: var(--color-zelenyy);
            margin-bottom: 8px;
        }

        &_fio {
            font-weight: 500;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
        }

        &_right {
            flex: 1 0 40%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
        }
    }


    &__form {
        border-radius: 10px;
        padding: 50px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--color-temno-zelenyy);
        color: var(--belyy);

        &_title {
            font-weight: 700;
            font-size: 30px;
            line-height: 140%;
            margin-bottom: 8px;
        }

        &_subtitle {
            margin-bottom: 30px;
        }

        &_inputs {
            display: flex;
            flex-direction: column;
            gap: 10px;   
        }

        &_input, &_textarea {
            background: var(--color-svetlo-zelenyy-2);
            color: var(--belyy);
            &::placeholder {
                color: var(--belyy);
            }
            &:focus {
                background: var(--color-svetlo-zelenyy-2);
            }
        }

        &_politic {
            color: var(--belyy);
        }
    }
}


// media page-faq-4
@media (max-width: 1150px) {
    .page-faq {
        &__grid {
            display: contents;
        }
        &__block {
            margin-bottom: 30px;
        }
        &__block_form {
            order: 5;
        }
        &__tab {
            &_top {
                flex-direction: column;
                align-items: stretch;
                gap: 30px;
                margin-bottom: 20px;
            }
        }
        &__list {
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 688px) {
    .page-faq {
        &__tabs {
            display: contents;
        }
        &__tab {
            &_btns {
                width: 100%;
                flex-direction: column;
            }
            &_btn {
                width: 100%;
            }
        }
        &__block {
            margin-bottom: 0;
        }
        &__button {
            border-radius: 10px;
            padding: 20px;
        }
        &__name {
            font-size: 16px;
        }
        &__editor {
            flex-direction: column;
            align-items: stretch;
            margin-top: 20px;
            border-radius: 10px;
            padding: 18px 20px;
            gap: 16px;
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            }
            &_img {
                border-radius: 60px;
                min-width: 64px;
                width: 64px;
                height: 64px;
            }
            &_text {
                font-size: 12px;
                margin-bottom: 2px;
            }
        }
        &__form {
            border-radius: 10px;
            padding: 20px;
            &_title {
                font-size: 18px;
            }
        }
        &__btn {
            margin-bottom: 10px;
        }
    }
}
