// gallery-page-3


.gallery-page__inner-3 {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    &>* {
        flex-basis: calc(25% - 15px);
    }
}

.gallery-page__tab {
    &_btns {
        display: flex;
        gap: 10px;
        margin-bottom: 50px;
    }
    
    &_btn {
        border-radius: 5px;
        padding: 8px 16px;
        background: var(--bg-svetlo-seryy);
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: var(--tekst-svetlyy);
        transition: all 0.3s ease;
    
        &.tab__btn--active {
            background: var(--color-svetlo-zelenyy-1);
            color: var(--tekst);
        }
    }

    &_btn-circle {
        display: none;
    }
}

.gallery-page__item-3 {
    max-height: 225px;
    min-height: 225px;
    position: relative;
}

.zoom {
    position: relative;
}

.zoom::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(61, 124, 133, 0.5);
    border-radius: 10px;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 2;
}

.zoom::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background: url("./../img/help/loupe.svg") no-repeat center center / cover;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 3;
}

.zoom:hover::before {
    opacity: 0.7;
}

.zoom:hover::after {
    opacity: 1;
}

.zoom:hover .gallery-page__item-descr-3 {
    opacity: 0;
}
.gallery-page__item-picture-3 {
    border-radius: 10px;
    position: relative;

    & img {
        border-radius: 10px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.gallery-page__item-descr-3 {
    position: absolute;
    background-color: #fff;
    color: #3D7C85;
    padding: 10px;
    border-radius: 5px;
    bottom: 4px;
    left: 4px;
    right: 4px;
    z-index: 3;
    height: auto;
    line-height: 120%;
    transition: opacity 0.3s ease;
    font-size: 14px;
}

// media gallery-page-3

@media (max-width: 1024px) {
    .gallery-page__inner-3 {
        &>* {
            flex-basis: calc(33.333% - 13.33333px);
        }
    }
}
@media (max-width: 850px) {
    .gallery-page__inner-3 {
        &>* {
            flex-basis: calc(50% - 10px);
        }
    }
}

@media (max-width: 550px) {
    .gallery-page__inner-3 {
        gap: 10px;
        &>* {
            flex-basis: 100%;
        }
    }

    .gallery-page__item-3 {
        max-height: 200px;
        min-height: 200px;
    }

    .gallery-page__item-descr-3 {
        padding: 10px;
    }

    .gallery-page__tab {
        &_btns {
            flex-direction: column;
            gap: 0;
            max-height: 54px;
            overflow: hidden;
            margin-bottom: 0;
            transition: all 0.3s linear;
            margin-bottom: 20px;
            &.active {
                max-height: 400px;
                .tab__btn--active {
                    border-radius: 5px 5px 0 0;
                }
                .tab__btn-circle {
                    transform: rotate(270deg);
                }
            }
        }
    
        &_btn {
            border-radius: 0;
            padding: 16px 0;
            background: transparent;
            text-align: left;
            border-bottom: 1px solid rgba(61, 124, 133, 0.25);
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 0 20px;
            .tab__btn-circle {
                opacity: 0;
            }
            &.tab__btn--active {
                border-radius: 5px;
                padding: 12px 18px;
                border-bottom: none;
                margin: 0;
                order: -1;
                .tab__btn-circle {
                    opacity: 1;
                }
            }
        }
    
        &_btn-circle {
            display: flex;
            background: rgba(61, 124, 133, 0.25);
            color: var(--tekst-svetlyy);
            transform: rotate(90deg);
        }
    }

}