// blockquote-4
.blockquote-4 {
    &__main {
        border-radius: 20px;
        padding: 40px;
        background: #eef9fa;
        position: relative;
        margin-bottom: 45px;
        &::before {
            content: "";
            position: absolute;
            top: 100%;
            left: 58px;
            display: block;
            border-left: 20px solid transparent;
            border-right: 48px solid transparent;
            border-top: 35px solid #eef9fa;
        }

        &_top {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 20px;
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: var(--color-temno-zelenyy);
            margin-bottom: 12px;
        }
    }

    &__editor {
        display: flex;
        align-items: center;
        gap: 20px;

        &_img {
            display: block;
            border-radius: 100px;
            min-width: 100px;
            width: 100px;
            height: 100px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &_text {
            font-size: 14px;
            line-height: 150%;
            color: var(--tekst-svetlyy);
            margin-bottom: 4px !important;
        }
    
        &_fio {
            font-size: 14px;
            & p:first-child {
                font-weight: 500;
                font-size: 20px;
                line-height: 140%;
                color: var(--tekst);
                margin-bottom: 8px;
            }
        }
    }

}


@media (max-width: 688px) {
    .blockquote-4 {
        &__main {
            border-radius: 10px;
            padding: 20px;
            margin-bottom: 37px;
            &::before {
                left: 36px;
                display: block;
                border-left: 12px solid transparent;
                border-right: 40px solid transparent;
                border-top: 25px solid #eef9fa;
            }
            &_top {
                flex-direction: row;
                align-items: center;
                gap: 16px;
                font-size: 14px;
                margin-bottom: 14px;
                & img {
                    width: 30px;
                    height: 23px;
                }
            }
        }
        &__editor {
            gap: 12px;
            &_img {
                min-width: 60px;
                width: 60px;
                height: 60px;
            }
            &_fio {
                font-size: 12px;
                & p:first-child {
                    font-weight: 700;
                    font-size: 14px;
                    margin-bottom: 4px;
                }
            }
        }
    }
}
