// error-2
.error {
    padding: 195px 0;
    position: relative;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__inner {
        max-width: 536px;
        margin-left: auto;
    }

    &__number {
        font-weight: 700;
        font-size: 254px;
        line-height: 100%;
        color: var(--color-temno-zelenyy);
        text-align: center;
        margin-bottom: 30px;
    }

    &__text {
        line-height: 140%;
        margin-bottom: 30px;
        & a {
            text-decoration: underline;
            color: var(--color-zelenyy);
            transition: all 0.3s ease;
            &:hover {
                color: var(--color-temno-zelenyy);
            }
        }
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        & li {
            width: calc(50% - 5px);
        }
    }
}

// media error-2
@media (max-width: 660px) {
    .error {
        padding: 40px 0;
        &__bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__inner {
            max-width: none;
            margin: 0;
        }
        &__number {
            font-size: 120px;
            text-align: left;
        }
        &__links {
            & li {
                width: 100%;
            }
        }
    }
}