.popup-change {
    &__content {
        max-width: max-content;
        border-radius: 20px;

        background: var(--tekst---svetlyy);
        display: flex;
        align-items: center;
    }

    &__left {
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
        margin-right: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-basis: 60%;
    }

    &__title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: -0.03em;
        text-align: center;
        color: var(--tekst---temnyy);
        margin-bottom: 20px;
    }

    &__subtitle {
        font-size: 16px;
        line-height: 150%;
        color: var(--tekst-svetlyy);
    }

    &__phone p {
        font-size: 14px;
        color: var(--tekst-svetlyy);
        margin-bottom: 6px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__input:hover {
        background-color: var(--belyy);
    }

    &__close {
        width: 40px;
        height: 40px;
        width: 30px;
        height: 30px;
        top: -55px;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.popup-change .form {
    width: 513px;
}

.form {
    padding: 40px;
    background-color: white;
    border-radius: 20px;
    z-index: 10;
}

.form .form__checkbox {
    border: 2px solid var(--osnovnoy---500);
    border-radius: 4px;
    width: 20px;
    height: 20px;
    background: var(--osnovnoy---500);
    cursor: pointer;
}

.custom-checkbox input[type="checkbox"] {
    display: none;
}

.checkbox-icon {
    display: inline-block;
    cursor: pointer;
    width: 20px;
    height: 20px;
    position: relative;
}

/* По умолчанию показываем серый чекбокс */
.checkbox-icon .checkbox-default {
    display: block;
}

.checkbox-icon .checkbox-checked {
    display: none;
}

.popup-change__content {
    position: relative;
}

.popup-change__picture {
    height: 368px;
    display: block;
    margin-left: -59px;
}

.popup-change__picture img {
    height: 100%;
    object-fit: fill;
    width: 100%;
    border-radius: 0 20px 20px 0;
}

.popup-reviews .popup-change__title {
    margin-bottom: 0;
}

.form__bottom {
    display: flex;
    gap: 12px;
    align-items: center;
    justify-content: center;
}

.popup-question .form {
    width: 620px;
}

/* Когда отмечено — скрываем серый, показываем красный */
.custom-checkbox input[type="checkbox"]:checked+.checkbox-icon .checkbox-default {
    display: none;
}

.custom-checkbox input[type="checkbox"]:checked+.checkbox-icon .checkbox-checked {
    display: block;
}


.form button {
    height: 52px;
}

.popup-change-response {
    &__content {
        max-width: 606px;
        padding: 10px;
        display: flex;
        flex-direction: column;
    }

    &__wrapper {
        & h3 {
            border-radius: 10px;
            padding: 30px 10px;
            background: var(--belyy);
            box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.06);
            text-align: center;
            font-weight: 700;
            font-size: 34px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 30px;
        }

        & p {
            display: flex;
            flex-direction: column;
            gap: 20px;
            padding: 0 30px 30px 30px;
            text-align: center;

            &::before {
                content: url("./../img/popups/popup-call-1-ok.svg");
            }
        }
    }

    &__btn {
        border-top: 1px solid #c7d6dc;
        padding-top: 30px;
        margin: 0 30px 30px;

        & a {
            display: block;
            width: 100%;
        }
    }
}

.popup[data-target="popup-change-ok"] {
    & p::before {
        content: url("./../img/popups/popup-call-1-ok.svg");
    }
}

.popup[data-target="popup-change-error"] {
    & p::before {
        content: url("./../img/popups/popup-call-1-error.svg");
    }

    & .popup-change-response__btn {
        display: none;
    }
}

.popup-reviews .popup-change__text {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form input {
    border-radius: 10px;
    padding: 12px 16px;
    width: 433px;
    height: 52px;
    background: #f5f5f5;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: var(--tekst---seryy);
}

.form textarea {
    border-radius: 10px;
    padding: 12px 16px;
    width: 100%;
    background: #f5f5f5;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: var(--tekst---seryy);
}

.input-top {
    display: flex;
    gap: 12px;
}

.form .input::placeholder {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: var(--tekst---seryy);
}

// media popup-change-1
@media (max-width: 1050px) {
    .popup-change__picture {
        display: none;
    }

    .popup-change .form {
        width: 100%;
    }

    .popup-question .form {
        width: 100%;
    }

    .popup-change {
        &__content {

            padding: 0px;
            flex-direction: column;
            max-width: 480px;
        }

        &__left {
            padding-right: 0;
            border-right: none;
            margin-right: 0;
            gap: 20px;
            flex-basis: 100%;
            align-items: flex-start;
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(190, 219, 215, 0.4);
            margin-bottom: 24px;
        }

        &__title {
            font-size: 18px;
        }

        &__subtitle {
            font-size: 14px;
        }

        &__phone {
            & a {
                font-size: 20px;
            }

            & svg {
                width: 20px;
                height: 20px;
            }
        }
    }

    .popup-change-response {
        &__content {
            max-width: 400px;
            padding: 10px;
            display: flex;
            flex-direction: column;
        }

        &__wrapper {
            & h3 {
                border-radius: 5px;
                padding: 16px 10px;
                font-size: 30px;
            }

            & p {
                gap: 14px;
                padding: 0 10px 20px;
            }
        }

        &__btn {
            padding-top: 20px;
            margin: 0 20px 20px;
        }
    }
}

@media (max-width: 600px) {
    .popup-change {
        &__close {
            width: 32px;
            height: 32px;
            top: -27px;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .input-top {
        flex-direction: column;
    }

    .popup-reviews .popup-change__text {
        flex-direction: column;
    }

    .popup-reviews .popup-change__title {
        margin-bottom: 12px;
    }

    .form {
        padding: 24px;
    }
}