// doctors-page-1
.doctors-page {
    &__top {
        margin-bottom: 25px;
    }

    &__sort {
        display: flex;
        gap: 6px;
        margin-bottom: 25px;

        &_block {
            flex: 1 0 30%;
        }
    }

    &__search {
        height: 100%;
        & input {
            height: 100%;
        }
    }

    &__quantity {
        font-size: 14px;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__card {
        display: block;
        padding-bottom: 20px;
        border-bottom: 1px solid #c7d6dc;

        &_photo {
            position: relative;
            border-radius: 10px;
            overflow: hidden;
            margin-bottom: 20px;
            max-height: 300px;
        }

        &_img {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        &_experience {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            color: var(--tekst-svetlyy);
            position: absolute;
            left: 14px;
            bottom: 14px;
        }

        &_fio {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 10px;
        }

        &_post {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst);
        }
    }
}


// media doctors-page-1
@media (max-width: 1100px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 800px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 688px) {
    .doctors-page {
        &__top {
            margin-bottom: 20px;
        }
        &__sort {
            flex-direction: column;
            gap: 12px;
            margin-bottom: 20px;
        }
        &__card {
            &_photo {
                max-height: 280px;
            }
            &_experience {
                padding: 6px 12px;
                left: 12px;
                bottom: 12px;
            }
            &_fio {
                font-size: 18px;
            }
        }
    }
}
@media (max-width: 500px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
    }
}

