.title-h1__jobs {
    max-width: 1000px;
}

.jobs__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
}

.jobs__item {
    border-radius: 10px;
    width: 335px;
    height: 335px;
    padding: 22px 24px;
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-color: #ebe6e6;
    cursor: pointer;
}

.jobs__item-svg {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
}

.jobs__item-title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---temnyy);
    max-width: 205px;
}

.jobs__item-num {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---temnyy);
    margin-bottom: 4px;
}

.jobs__item-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
}

.title-h1__job-page {
    max-width: 1000px;
    margin-bottom: 48px;
}

.head-job__items {
    display: flex;
    align-items: center;
}

.head-job__item {
    display: flex;
    gap: 16px;
    width: 262px;
    height: 60px;
}

.head-job__item-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 4px;
}

.head-job__item-bold {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---temnyy);
}

.head-job__item-light {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
}

.job-page__items {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}

.job-page__item {
    flex: 0 0 47%;
}

.job-page__item-title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 32px;
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--tekst---temnyy);
    margin-bottom: 24px;
}

.job-page__item-list ol li {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: var(--tekst---seryy);
}

@media (max-width: 1100px) {
    .jobs__items {
        justify-content: center;
        align-items: center;
    }
}

@media (max-width: 900px) {
    .head-job__items {
        align-items: start;
        flex-direction: column;
    }

    .job-page {
        padding-top: 0;
    }

    .job-page__items {
        gap: 31px;
    }

    .job-page__item {
        flex: 0 0 100%;
    }

    .job-page__items .job-page__item ol {
        padding-bottom: 0;
        margin-bottom: 0;
    }

    .job-page__item-title {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: -0.03em;
        color: var(--tekst---temnyy);
    }


    .head-job__item svg {
        height: 46px;
    }

    .head-job__item:not(:last-child) {
        border-bottom: 1px solid rgba(22, 22, 22, 0.07);
        padding-bottom: 16px;
        margin-bottom: 16px;
        height: auto;
    }
}

@media (max-width: 800px) {
    .title-h1__job-page {
        margin-bottom: 20px;
    }

    .head-job__item-bold {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--tekst---temnyy);
    }

    .head-job__item-light {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--tekst---seryy);
    }
}







@media (max-width: 495px) {
    .jobs__item {
        width: 100%;
    }
}